import {stateProvider} from '../services/stateManagement/stateProvider'

const substituteVariable = (text) => {

    var globalVariables = stateProvider.current.questionnaire._globalVariables;

    var keys = Object.keys(globalVariables);

    var currentText = text;

    keys.forEach(name=>{
        
        if (currentText.includes(name)){
            currentText = currentText.replace(name, globalVariables[name]);
        }

    });

    return currentText;


};

export default substituteVariable;

export const replaceVariable = (item, props) => {

    if (props.replaceVariables === true){

        item = substituteVariable(item);
    }

    return item;
};

export const replaceOptions  = (options, props)=>{

    if (props.replaceVariables === true){
        var temp = [...options];

        temp.forEach(option=>{
            option.label = substituteVariable(option.label);

        });
        return temp;

    }else{
        return options;
    }

}

export const findGlobalVariable = (key)=>{

    var globalVariables = stateProvider.current.questionnaire._globalVariables;

    return globalVariables[key];

};

