import * as types from '../actions/ActionTypes'
import produce from 'immer'
import ConditionalEvaluator from '../common/ConditionalEvaluator'

export default function updateGlobalVariableReducer(state, action){
   
    const nextState = produce(state, draft => {

        const globalVariableKey = "_globalVariables";
        switch (action.type){

            case types.UPDATE_GLOBAL_VARIABLE: {            
                const data = action.data;
               

                data.forEach(dataItem => {
                    
                    var canUpdate = checkCondition(dataItem.condition, state);

                    if (canUpdate === true){
                        var globalVariables = draft.questionnaire[globalVariableKey];
                        dataItem.variables.forEach(variable => {
                            let name = variable.name;
                            let value = variable.value;
                            globalVariables[name] = value;
                        });
                        
                    }

                });

                break;
            }

            default:
                break;
        }
    });

    return nextState;
    

}

const checkCondition = (condition, state)=>{

    if (typeof condition === "undefined" || condition === null){
        return true;
    }

    if (Array.isArray(condition) && condition.length === 0){
        return true;
    }
    return ConditionalEvaluator.evaluate(condition, null, state);


}