import React, { useState, useRef } from "react";
import INFO_ICON_BLUE from "../../assets/img/info_light_blue.app.png";

import "./style.css";

const NGISTextInputComponent = ({
  labelText,
  orientation,
  disabled,
  placeholder,
  required,
  maxChar,
  infoText,
  onChange,
  value,
  requiredIcon,
  ...rest
}) => {
  const ngisTextInputRef = useRef(null);

  const [currentText, setCurrentText] = useState("");

  if (!Number.isInteger(maxChar)) {
    maxChar = 30;
  }

  if (placeholder === null || placeholder == undefined) {
    placeholder = "";
  }

  let isInline = false;

  const parseOrientation = (orientationText) => {
    try {
      const temp = orientationText.toLowerCase();
      if (temp === "inline") {

        return true;
      } else if (temp === "top") {
        return false;
      }
    } catch (e) {
      console.log(e);
      throw 'Unable to parse orientation';

    }
  }

  if (orientation !== null && orientation !== undefined) {
    isInline = parseOrientation(orientation);
  }

  const handlePaste = ({ event, inputType }) => {

  };


  const events = ({ inputType, max }) => ({
    onPaste: (event) => handlePaste({ event, inputType, max }),
  });





  const getTextInputContainerClass = () => {

    if (isInline) {
      return 'text-input-container-horizontal';
    } else {
      return 'text-input-container-vertical';
    }

  };

  const getRequiredIcon = () =>{

    if (requiredIcon === true){
      return (
        <div className="required-asterisk">✱</div>
      )
     
    }
    return null;

  }

  const getInfoMessage = ()=>{

    if (infoText === null || typeof infoText === "undefined"){
      return null;
    }

    return (

      <div className="admin-support-help">
        <img className="admin-support-icon" src={INFO_ICON_BLUE} />
        <p className="admin-support-info">{infoText}</p>
      </div>
      
    )
  }

  return (
    <div className={getTextInputContainerClass()}>
      <label>{labelText} {getRequiredIcon()}</label>
      <input
        type="text"
        max={maxChar}
        placeholder={placeholder}
        ref={ngisTextInputRef}
        className = "text-input"
        disabled={disabled}
        onChange={onChange}
        value={value}
      >
      </input>
      {getInfoMessage()}
      
    </div>



  )
};

export default NGISTextInputComponent;