import * as types from './ActionTypes'

export const ngisPanelMoveToPanelStep = (data) => ({
  type: types.NGIS_PANEL_MOVE_TO_PANEL_STEP,
  panelIndex : data.panelIndex,
  parentPanelId : data.parentPanelId,
  condition : data.condition

});


export const ngisPanelMoveNextAction = (data) =>
{
    return {
      type: types.NGIS_PANEL_MOVE_NEXT,
	    panelIndex : data.panelIndex,
      parentPanelId : data.parentPanelId,
      runIf : data.runIf
    }
}
