import React from "react";
import {useSelector} from '../../services/stateManagement/store'
import ValidationSummaryComponent from './ValidationSummaryComponent'

export const ValidationSummaryContainer = (props) => {

    const validationGroupId = props.validationGroupId;

    let visible = false; //initial
    let messages = []; 

    const validationInfo = useSelector((state)=> {  
    
        
        if (state && state.questionnaire && state.questionnaire && state.questionnaire._validationResult && state.questionnaire._validationResult[validationGroupId]){
          return state.questionnaire._validationResult[validationGroupId];
        }
        return null;
      
    });
    
    if (validationInfo !== null){
        visible = validationInfo.isValid === false;
        messages = validationInfo.messages;
    }


    return (

        <ValidationSummaryComponent id={props.id} visible={visible} messages={messages}></ValidationSummaryComponent>

    )
};