import React, { useState, useRef } from "react";
import renderComponentModel from '../../services/rendering/ComponentRenderer'

import "./style.css";

const SectionComponent = (
{
    id,
    children,
    visible,
    ...rest
}) => {


    const renderChildren = () => {
        
        if (children){
            
            return (
                <React.Fragment>
                    {children.map((value, index) => {
                        return (
                            <div  key={index}>
                                {renderComponentModel(value)}    
                            </div>
                        )
                    })}
                </React.Fragment>

            )
    
        }
        return  null
    }

    
    if (!visible){
        return null;
    }

    return (
        <div>
            {renderChildren()}   
        </div>
        
    )


}

export default SectionComponent;