import * as types from './ActionTypes'

export const fileAddedAction = (data) => ({
  type: types.NGIS_FILEUPLOAD_FILE_ADDED,
  id : data.id,
  files : data.files

});

export const fileRemovedAction = (data) => ({
  type: types.NGIS_FILEUPLOAD_FILE_REMOVED,
  displayName : data.displayName,
  id : data.id

});

export const fileTypeUpdatedAction = (data) => ({
  type: types.NGIS_FILEUPLOAD_FILETYPE_UPDATED,
  displayName : data.displayName,
  fileType : data.fileType,
  id : data.id

});

export const fileDateUpdatedAction = (data) => ({
  type: types.NGIS_FILEUPLOAD_FILEDATE_UPDATED,
  displayName : data.displayName,
  fileDate : data.fileDate,
  id:data.id

});

export const fileUploadErrorAction = (data) => ({
  type: types.NGIS_FILEUPLOAD_FILE_ERROR,
  error : data.error,
  id:data.id

});




