import React, { useState, useRef } from "react";

import iconWarning from './images/alert-icon-warning.svg';
import "./infoMessageStyles.css";

const NGISFileUploadInfoMessage = (
{
    messages,
    alertType,
    ...rest
}) => {

    console.log(JSON.stringify(messages));
    const renderChildren = () => {
        
        if (messages){
            
            return (
                <React.Fragment>
                    <ul className="message-list">

                   
                        {messages.map((message, index) => {
                            if (message.messageType === "message-line"){
                                return (
                                    <li className={getMessageLineClass()} key={index}>
                                        {message.messageText}
                                    </li>
                                    
                                )
                            }else{
                                return (
                                    <li className="message-bullet" key={index}>
                                        {message.messageText}
                                    </li>
                                    
                                )
                            }
                            
                            
                        })}
                     </ul>
                </React.Fragment>

            )
    
        }
        return  (<React.Fragment></React.Fragment>)
    }

    const getAlertContainerClass = () => {

        const baseCSS = "fileupload-cs-alert-container";
        switch (alertType.toLowerCase()) {
            case "info":
                return baseCSS + " " + "fileupload-cs-alert-info";               
            case "warning":
                return baseCSS + " " + "fileupload-cs-alert-warning";
            case "success":
                return baseCSS + " " + "fileupload-cs-alert-success";             
            case "error":
                return baseCSS + " " + "fileupload-cs-alert-error"; 
            default:
                return baseCSS + " " + "fileupload-cs-alert-info";    //default to info      
        }  
    }

    const getMessageLineClass = () => {
        const baseCSS = "message-line";
        switch (alertType.toLowerCase()) {
            case "info":
                return baseCSS + " " + "fileupload-cs-message-line-info";               
            case "warning":
                return baseCSS + " " + "fileupload-cs-message-line-warning";
            case "success":
                return baseCSS + " " + "fileupload-cs-message-line-success";             
            case "error":
                return baseCSS + " " + "fileupload-cs-message-line-error"; 
            default:
                return baseCSS + " " + "fileupload-cs-message-line-info";    //default to info      
        }  

    }

    

    return (
        <div className={getAlertContainerClass()}>
            { renderChildren()}   
        </div>
        
    )


}

export default NGISFileUploadInfoMessage;