import React, { useState, useRef, useEffect } from "react"
import { propertyExistAndHasValue } from "../../common/HelperFunctions";
import "./style.css";

const NGISRadioButtonComponent = (
{
    id,
    labelText,
    options,
    required,
    disabled,
    initialValue,
    actions,
    onClicked,
    orientation,
    ...rest
}) => {

    const [selectedValue, setSelectedValue] = useState(initialValue);

    const splitChar = "----";

    console.log("initial value " + initialValue + " selected value : " + selectedValue);

    //console.log("options=" + JSON.stringify(options));

    const isSelectedOption = (value) => {

        return selectedValue === value;        
    }

    

    const handleChangeEvent = (changeEvent) => {
        // there to prevent react from complaining
        console.log(changeEvent.target.value);
    }

    const handleClick = (clickEvent) => {        
        const [value, index] = clickEvent.target.id.split(splitChar);

        if (options[index].disabled){
            return;
        }

        if (selectedValue !== value){
            console.log("Setting new version to " +value);
            setSelectedValue(value);

            onClicked({selectedValue:value, selectedIndex:index});
        }
    }

    const getOptionsClass =() =>{

        let classname = "ngis-radio-options";

        if (orientation && orientation.toLowerCase() === "horizontal"){
            classname = "ngis-radio-options horizontal";
        }

        return classname;

    }

    const getRadioClass =(option) =>{

        let classname = option.disabled ? 'radio-disabled' : 'radio';

        if (orientation && orientation.toLowerCase() === "horizontal"){
            classname = classname + " horizontal";
        }

        return classname;

    }

    return (
        <div className="ngis-radio-questions">
            <div className='question-title'>
                {labelText}
            </div>
            <div className={getOptionsClass()}>

                {options !== undefined && options !== null && options.map((option, index) => {
                    return (
                        <div className={getRadioClass(option)} key={index}>
                            <input type="radio" name={id} value={option.value} aria-checked="false" id={index} checked={isSelectedOption(option.value)} onChange={handleChangeEvent} data-testid={'radio'}/>
                            <label key={index} id={option.value + splitChar + index} onClick={handleClick} aria-labelledby={option.value + index}>
                                {option.label}
                            </label>
                        </div>
                    )
                    }
                )}
             </div>
        </div>
        
    )


}

export default NGISRadioButtonComponent;