import React, { useState, useRef } from "react";
import ButtonComponent from './ButtonComponent';
import dispatchElementActions from '../../common/actionHelpers'
import {useSelector, useDispatch} from '../../services/stateManagement/store'

export const ButtonContainer = (props) => {
    
  const dispatch = useDispatch();


  let defaultDisabled = false;

  if (props.disabled === true){
    defaultDisabled = true;
  }

  let disabled = useSelector((state)=> {  
      if (state && state.questionnaire && state.questionnaire[props.id]){
          return state.questionnaire[props.id].disabled;
      }
      return defaultDisabled;
    
    });

    if (disabled === null || typeof disabled === "undefined"){
      disabled = defaultDisabled;
  }

  let defaultVisible = props.visible === false ? false : true;

  let visible = useSelector((state)=> {  
    if (state && state.questionnaire && state.questionnaire[props.id]){
        return state.questionnaire[props.id].visible;
    }
    return defaultVisible;
  
  });

  if (disabled === null || typeof disabled === "undefined"){
    disabled = defaultDisabled;
}

  const buttonClick = (e) => {
    
    e.preventDefault();
    
    console.log("Button clicked");

    if (!props.actions){
      return false;
    }


    dispatchElementActions(props, dispatch);
  }

  if (visible===false){
    return null;
  }
  
  
  return (
    <ButtonComponent disabled={disabled} labelText={props.labelText} onClick = {buttonClick} ></ButtonComponent>
  )
};