
import ConditionalEvaluator from './ConditionalEvaluator';


export const processEnableWhen = (currentState, newState, element) =>{

    const item = element.enableWhen;

    const passes = ConditionalEvaluator.evaluate(item, null, currentState);
    //console.log("Processing enable when");

    if (element.type.toLowerCase()==="button"){

        const elementId = element.id;
        newState.questionnaire[elementId].disabled = !passes;

        
    }


        
        
    

}


