
import extractElementValue from '../common/extractElementValue'
import extractValidationGroupResult from '../common/extractValidationResult'
import Utils from './HelperFunctions'

const LogicalEvaluator = (logicalItem, state) => {

    if (isElementPropertyCondition(logicalItem)){
        return evaluateElementPropertyCondition(logicalItem, state);
    }else if (isValidationGroupCondition(logicalItem)){
        //console.log("Evaluating validation group item");
        return evaluateValidationGroupResult(logicalItem, state);
    }

    throw new Error('unsupported logical item');


};

const evaluateElementPropertyCondition = (logicalItem, state)=> {

    const id = logicalItem.elementId;
    const property = logicalItem.property;
    const operator = logicalItem.operator;
    const value2 = logicalItem.value;

    if (property == "uploadedFiles"){
        debugger;
    }

    const value = extractElementValue(id, property, state);

    return Utils.compareOperation(value, value2, operator);

    

};

const evaluateValidationGroupResult = (logicalItem, state)=>{

    const validationGroup = logicalItem.validationGroup;
    const property = logicalItem.property;
    const operator = logicalItem.operator;
    const value2 = logicalItem.value;

    const value = extractValidationGroupResult(validationGroup, property, state);

    const result = Utils.compareOperation(value, value2, operator);

    //console.log("Validation Group result value " + result);

    return result;


}

const isElementPropertyCondition = (logicalItem) =>{

    return "elementId" in logicalItem && "operator" in logicalItem && "value" in logicalItem;
}

const isValidationGroupCondition = (logicalItem) => {

    return "validationGroup" in logicalItem && "operator" in logicalItem && "value" in logicalItem;
}

export default LogicalEvaluator;