import * as types from '../actions/ActionTypes'
import produce from 'immer'

export default function ngisFileUploadReducer(state, action){

    const nextState = produce(state, draft => {
        switch (action.type){
            
            case types.NGIS_FILEUPLOAD_FILE_ADDED: {
                
                var filesAdded = action.files;
                var currentFiles = state.questionnaire[action.id].uploadedFiles;
                console.log("File upload reducer component id " + action.id);
                filesAdded.forEach(addedFile => {
                    if (currentFiles.findIndex(element=>element.displayName===addedFile.displayName) === -1){
                        draft.questionnaire[action.id].uploadedFiles.push({
                            uniqueName : addedFile.uniqueName,
                            displayName : addedFile.displayName,
                            fileMetaData : addedFile.fileMetaData,
                            loaded: false,
                            fileType : null,
                            fileDate : null
                        });
                    }else if (addedFile.loaded === true){
                        var index = currentFiles.findIndex(element=>element.displayName === addedFile.displayName);
                        if (index !== -1){
                            draft.questionnaire[action.id].uploadedFiles[index].loaded = true;
                        }
                    }

                });
                
                break;
            }
            case types.NGIS_FILEUPLOAD_FILETYPE_UPDATED:
                var displayName = action.displayName;
                var fileType = action.fileType;

                var currentFiles = state.questionnaire[action.id].uploadedFiles;
                var index = currentFiles.findIndex(element=>element.displayName == displayName);

                if (index !== -1){
                    draft.questionnaire[action.id].uploadedFiles[index].fileType = fileType;
                }
                
            
                break;
            case types.NGIS_FILEUPLOAD_FILEDATE_UPDATED:
                var displayName = action.displayName;
                var fileDate = action.fileDate;

                var currentFiles = state.questionnaire[action.id].uploadedFiles;
                var index = currentFiles.findIndex(element=>element.displayName === displayName);

                if (index !== -1){
                    draft.questionnaire[action.id].uploadedFiles[index].fileDate = fileDate;

                    var isValid = isFileUploadStateValidToContinue(currentFiles, fileDate, index);
                    draft.questionnaire[action.id].isValid = isValid;
                    
                }
                break;
                case types.NGIS_FILEUPLOAD_FILE_REMOVED:
                    var displayName = action.displayName;
                    var currentFiles = state.questionnaire[action.id].uploadedFiles;
                    var index = currentFiles.findIndex(element=>element.displayName ===displayName);
    
                    if (index !== -1){
                        draft.questionnaire[action.id].uploadedFiles.splice(index,1);
                        var isValid = isFileUploadStateValidToContinue(draft.questionnaire[action.id].uploadedFiles, null, null);
                        draft.questionnaire[action.id].isValid = isValid; 
                    }

                break;
               case  types.NGIS_FILEUPLOAD_FILE_ERROR:     
                    console.log("In upload error reducer" + JSON.stringify(action.error));
                    draft.questionnaire[action.id].errorMessages = action.error;

               
                break;

            default:
                break;
        }



    });
    //console.log("Next state in radio reducer is " + JSON.stringify(nextState));
    return nextState;
    

}

const isFileUploadStateValidToContinue = (currentFiles, lastestFileDate, index) =>{

    let isValid = true;

    for (let i=0; i<currentFiles.length; i++){
        if (i == index){
            isValid = lastestFileDate !== null && lastestFileDate !== undefined && lastestFileDate.isValid === true;
        }else{
            isValid = currentFiles[i].fileDate && currentFiles[i].fileDate.isValid === true;
        }
        if (!isValid){
            break;
        }
    }

    return isValid;
}