import React from "react";
import {useSelector, useDispatch, AppStateContext} from '../../services/stateManagement/store'

import {fileAddedAction,fileRemovedAction,fileTypeUpdatedAction,fileDateUpdatedAction, fileUploadErrorAction}  from '../../actions/NGISFileUploadActions'
import NGISFileUploadComponent from './NGISFileUploadComponent';
import  {findGlobalVariable} from '../../common/substituteVariables'

export const NGISFileUploadContainer = (props) => {

    const defaultValue = {
        uploadedFiles : [],
        isValid : false,
        maxFormsReached: false,
        errorMessages : []
    };

    let currentValue = useSelector((state)=> {  
        if (state && state.questionnaire && state.questionnaire[props.id]){
            return state.questionnaire[props.id];
        }
        return defaultValue;
      
      });

    if (currentValue === null || typeof currentValue === "undefined"){
        currentValue = defaultValue;
    }

    const dispatch = useDispatch();

    var fileTypeOptionsKey = props.fileTypeOptions;

    let fileTypeOptions = [];

    if (typeof fileTypeOptionsKey === "string" && fileTypeOptionsKey.startsWith("$")){      
        fileTypeOptions = extractOptionsGlobalVariable(fileTypeOptionsKey);
    }else{
        fileTypeOptions = props.fileTypeOptions;
    }

    const onChange = (data) => {
         if (data.eventType==="file-added"){
            dispatch(fileAddedAction({id:props.id, files: data.files}));
        }else if (data.eventType==="file-removed"){
            dispatch(fileRemovedAction({id:props.id, fileName: data.fileName, displayName: data.displayName}));
        }else if (data.eventType === "file-date-updated"){
            dispatch(fileDateUpdatedAction({id:props.id, fileName: data.fileName, displayName: data.displayName, fileDate: data.fileDate}));
        }else if (data.eventType === "file-type-updated"){
            dispatch(fileTypeUpdatedAction({id:props.id, fileName: data.fileName, displayName: data.displayName, fileType: data.fileType}));
        }else if (data.eventType === "file-upload-error"){
            dispatch(fileUploadErrorAction({id:props.id, error : data.error}));
            console.log("upload error dispatched");
        }
        else{
            throw new Error(`Unrecognised event type ${data.eventType}`);
        }

    };

    const dateInputData = {dateInputLabel : props.dateInputLabel,  minimumDate: props.dateMininumDate, minimumDateMessage : props.minimumDateMessage, requiredDateMessage : props.requiredDateMessage };

    const isDisabled = fileTypeOptions.length <= currentValue.uploadedFiles.length && currentValue.uploadedFiles.length>0;



    return(
        <NGISFileUploadComponent 
        id={props.id} 
        uploadedFiles={currentValue.uploadedFiles}
        disabled={isDisabled}
        comboBoxItems={props.comboBoxItems} 
        dateInputData={dateInputData}
        fileTypeOptions={fileTypeOptions}
        onChange={onChange}
        errorMessages={currentValue.errorMessages}
        
        >
        </NGISFileUploadComponent>
    )

};

const extractOptionsGlobalVariable = (key) => {

    var optionsText = findGlobalVariable(key);

    if (!(optionsText === undefined || optionsText === null )){

        var items = optionsText.split("|");

        return items.map(item=>{

            return {
                value : item,
                name : item
            }

        });
    }

    return [];

}

 

