import React, { useState, useRef } from "react";
import {useSelector} from '../../services/stateManagement/store'

const createVisibleSelector = (id, defaultValue) =>{

    return ()=>{
        const defaultVisible = defaultValue;

        let visible = useSelector((state)=> {  
            if (state && state.questionnaire && state.questionnaire[id]){
                return state.questionnaire[id].visible;
            }
            return defaultVisible;
        
        });

        if (visible === null || typeof visible === "undefined"){
            visible = defaultVisible;
        }

        return visible;

    };
    

}

export default createVisibleSelector;

