import React, { useState, useRef } from "react";
import {CollapsibleLinkComponent} from './CollapsibleLinkComponent';
import createVisibleSelector from '../../common/selectors/visibleSelector'

export const CollapsibleLinkContainer = (props) => {
    
  let visible = createVisibleSelector(props.id, true)();

  if (!visible){
        
    return null;
  }


  return (
    <CollapsibleLinkComponent children={props.item} openMessage={props.openMessage} closeMessage={props.closeMessage}  />
  )
};
