import React from 'react'
import {DateInputContainer} from './DateInputContainer'


export function DateInputProvider(item){ 
    const minDate = item.minDate;

    const props = {...item};

    if (typeof props.minimumDate === "undefined"){
        props.minimumDate='1900-01-01';
    }
    

    return (

        <DateInputContainer {...props} />
    )
}