import React, { useState, useRef, useEffect, useLayoutEffect } from "react";
import renderComponentModel from "../../services/rendering/ComponentRenderer";
import inputPreprocessor from "./inputPreprocessor";
import { stateProvider } from "../../services/stateManagement/stateProvider";
import {
  useSelector,
  useDispatch,
  flags,
} from "../../services/stateManagement/store";
import * as actionTypes from "../../actions/ActionTypes";

const RootComponentProvider = ({ questionModelText, ...rest }) => {
  console.log("Root component called");

  const [questionModel, appSate] = inputPreprocessor(questionModelText);

  //stateProvider.initialState = appSate;

  const dispatch = useDispatch();

  //console.log("Root component appState : " + JSON.stringify(appSate) );
  //stateProvider.initialState = appSate;
  //stateProvider.current = appSate;

  useEffect(() => {
    if (flags.init === false) {
      if (questionModel !== null) {
        dispatch({ type: actionTypes.APP_INIT_STATE, payLoad: appSate });
        flags.init = true;
      }
    }
  }, [questionModelText]);

  const renderQuestionModel = () => {
    if (questionModel === null || questionModel === undefined) {
      return <div className="no-component">.</div>;
    }

    if (!Array.isArray(questionModel.item)) {
      return showRenderingError("item not found or is not an array");
    }

    return (
      <React.Fragment>
        {questionModel.item.map((value, index) => {
          return renderComponentModel(value, value.id);
        })}
      </React.Fragment>
    );
  };

  // const renderModel = (item, index) => {
  //   const componentType = item.type;

  //   const provider = componentProviderMapping[componentType.toLowerCase()];

  //   if (!provider){
  //       throw ("Invalid provider");
  //   }

  //   return (
  //       <React.Fragment key={index}>
  //           {provider(item)}
  //       </React.Fragment>
  //   )

  // }

  const showRenderingError = (message) => {
    return <div className="rendering-error">{message}</div>;
  };

  return <div>{renderQuestionModel()}</div>;
};

export default RootComponentProvider;
