import produce from 'immer'
import validatorProcessor from '../common/ValidatorProcessor'
import * as types from '../actions/ActionTypes'

export default function validatorReducer(state={}, action){

    const nextState = produce(state, draft => {

        if (action.type === types.VALIDATOR_VALIDATE){

            //console.log("Validation triggered");

            const validatorIds = action.validatorIds;

            var results = validatorProcessor(validatorIds, state, draft);

            results.forEach(result => {
                const validationGroup = result.validationGroup;
                draft.questionnaire._validationResult[validationGroup]={
                    isValid : result.isValid,
                    messages : result.messages

                }

            });

            console.log("Validation group results " + JSON.stringify(results));


        }
        
    });
    
    
    return nextState;
    

}
