
import ConditionalEvaluator from './ConditionalEvaluator';
import Utils from './HelperFunctions'

export const processVisibleWhen = (currentState, newState, element) =>{

    const item = element.visibleWhen;

    const passes = ConditionalEvaluator.evaluate(item, null, currentState);

    if (element.type.toLowerCase()==="ngis-panelstep"){

        // I need parent property id to change visible panel steps
        // add parentpanelId to
        const parentPanelId = element.parentId;

        var panelVisibilityList = [...currentState.questionnaire[parentPanelId].panelVisibilityList];

        //console.log("panelVisibilityList " + JSON.stringify(panelVisibilityList));

        var panelStep = panelVisibilityList.find(u=>u.id === element.id);

        var panelIndex = panelVisibilityList.findIndex(u=>u.id === element.id);

        //console.log("panel step " + JSON.stringify(panelStep));
        panelStep = Utils.unfreeze(panelStep);

        panelStep.visible = passes ? true : false;

        //console.log("Passes =" + passes);

        newState.questionnaire[parentPanelId].panelVisibilityList[panelIndex].visible = passes;
        
    }else {


        const elementId = element.id;
        newState.questionnaire[elementId].visible = passes;
        console.log("Visible when element id "+ elementId + " result = " + passes);

    }


        
        
    

}


