import React, { useState, useRef } from "react";
import renderComponentModel from '../../services/rendering/ComponentRenderer'



export const FlexboxComponent = (
{
    id,
    containerStyle,
    children,
    ...rest
}) => {

   const getContainerStyle = () =>{

        const style = {};

        if (!containerStyle || (Array.isArray(containerStyle) && containerStyle.length ===0)){
            style["diplay"] = "flex";
            return style;
        }

       return extractStyleFromProps(containerStyle);
   };


   const extractStyleFromProps = (styleProps) =>{

        const style = {};

        if (!styleProps || (Array.isArray(styleProps) && styleProps.length ===0)){
            return null;
        }

        styleProps.forEach(item => {
            var tokens = item.split(":");
            
            if (tokens.length!==2){
                console.error("Invalid property in flexbox id="+ id + " property="+item);
            }
            style[tokens[0].trim()]=tokens[1].trim();

        });

        return style;

   }

    const renderChildren = () => {      
        if (children){       
            return (
                <React.Fragment>
                    {children.map((value, index) => {
                        return (
                            <div className="fb-flex-item" style={extractStyleFromProps(value.itemFlexStyle)} key={index}>
                                {renderComponentModel(value)}    
                            </div>
                        )
                    })}
                </React.Fragment>

            )   
        }
        return  (<React.Fragment></React.Fragment>)
    }

    

    return (
        <div style={getContainerStyle()}>
            {renderChildren()}   
        </div>
        
    )
    

}