import React, { useState, useRef } from "react";
import renderComponentModel from '../../services/rendering/ComponentRenderer'

//import "./style.css";

export const PageSectionComponent = (
{
    pages,
    currentindex,
    ...rest
}) => {

    if (currentindex === null || currentindex === undefined){
        currentindex = 0;
    }

    const  [pageIndex, setPageIndex] = useState(currentindex);

    const pageLength = pages.length;
    
    const showPreviousPage = () =>{

        if (pageIndex === 0){
            return;
        }

        setPageIndex(pageIndex-1);

    }

    const showNextPage = () => {
        if (pageIndex+1 > pageLength-1){
            return;
        }
        setPageIndex(pageIndex+1);
    }

    const showPage = (newPageIndex) => {

        if (newPageIndex<0 || newPageIndex > pageLength-1){
            return;
        }
        setPageIndex(newPageIndex);

    }

    const setPageData = (pageData) => {

        //used to store global page data so we retrieve it
    }

    // const pageFunctions = ({ inputType, max }) => ({
    //     onKeyUp: (event) => handleKeyUp({ event, inputType, max }),
    //     onKeyDown: (event) => handleKeyDown({ event, inputType, max }),
    //     onChange: (event) => handleChange({ event, inputType, max }),
    //     onPaste: (event) => handlePaste({ event, inputType, max }),
    //     onBlur: (event) => handleBlur({ event, inputType, max }),
    //   });

  
    const renderChildren = () => {
       
        if (pages){
            console.log("Page container : rendering children");
            return (
                <React.Fragment>
                    {pages.map((value, index) => {
                        var canRender = currentindex == index;
                        console.log("Can render " + index + " =" + canRender);
                        return (
                           canRender && <div className="page-item" key={index}>
                                {renderComponentModel(value)}    
                            </div>
                        )
                    })}
                </React.Fragment>

            )
    
        }
        return  (<React.Fragment></React.Fragment>)
    }

    

    return (
        <div className="page-container">
            {renderChildren()}   
        </div>
        
    )


}

export default PageSectionComponent;