import * as types from '../actions/ActionTypes'
import produce from 'immer'
import { questionnaireInputData } from '../services/questionnaireInputData'
import {stateInitializerMapping} from '../services/stateInitializerMapper'

export default function clearValuesReducer(state, action){
  
    const nextState = produce(state, draft => {

        switch (action.type){

            case types.QUESTIONNAIRE_CLEAR_VALUES: {

                const elementIds = action.ids;

                var elementData = questionnaireInputData.normalized;
                


                elementIds.forEach(id => {
                    let elementType;
                    try{
                         elementType = elementData[id].type.toLowerCase();
                    }catch (e){
                        console.error("Error getting element type " + id + " " + e);
                        throw new Error(e);
                    }
                   

                    var [value, property] = getDefaultState(elementType, elementData);

                    if (property === null){
                        draft.questionnaire[id] = value;
                    }else{
                        draft.questionnaire[id][property] = value;
                    }
                    
                   

                });

                break;
            }

            case types.QUESTIONNAIRE_CLEAR_VALIDATION_RESULT:{

                const elementIds = action.ids;

                elementIds.forEach(id => {
                    
                    draft.questionnaire["_validationResult"][id]=null;

                });
            }
            break;

            case types.QUESTIONNAIRE_CLEAR_ENSUING_VALUES:{

                console.log("QUESTIONNAIRE_CLEAR_VALUES called");
                const startElementId = action.id;

                const indexOfElement = questionnaireInputData.ordered.findIndex(u=>u.id===startElementId);

                var elementData = questionnaireInputData.normalized;

                if (indexOfElement === -1){
                    throw new Error("Error clearing ensuing values. id not found " + startElementId);
                }

                let startIndex = indexOfElement;
                let total = questionnaireInputData.ordered.length;

               
                for (let i = startIndex; i < total; i++){
                    var elementInfo = questionnaireInputData.ordered[i];

                    if (elementTypesToIgnoreWhenClearingState.indexOf(elementInfo.type.toLowerCase())===-1){
                        
                        var [value, property] = getDefaultState(elementInfo.type, elementData);

                        if (elementInfo.id === "PS4-A-Q2"){
                            console.log("Clearing PS4-A-Q2");
                        }

                        if (property === null){
                            draft.questionnaire[elementInfo.id] = value;
                        }else{
                            draft.questionnaire[elementInfo.id][property] = value;
                        }
                    }

                }

            }
            default:
                break;
        }



    });

    return nextState;
    

}

const getDefaultState = (elementType, elementData)=>{

    var initialStateProvider=stateInitializerMapping[elementType];

    if (initialStateProvider !== null && initialStateProvider !== undefined){
        let defaultState = initialStateProvider(elementData);
        return [defaultState, null];
    }

    //default to value
    return [null, "value"];

}

const elementTypesToIgnoreWhenClearingState = [
    "pagesection",
    "page",
    "section",
    "ngis-panel",
    "ngis-panelstep",
    "layout"
];
