import React, { useState, useRef } from "react";
import renderComponentModel from '../../services/rendering/ComponentRenderer'
import eventManager from '../../services/events/eventManager'
import {eventTypes} from '../../services/events/eventAndActionTypes'
import "./style.css";
import NGISQuestionAnswerSummaryComponent from "../NGISQuestionAnswerSummary/NGISQuestionAnswerSummaryComponent";

export const NGISPanelStepComponent = (
{
    id,
    children,
    label,
    panelIndex,
    panelStepNumber,
    currentState,
    summaryButtonAction,
    panelStepClick,
    summary,
    displayedPanelStepId,
    ...rest
}) => {

   const [panelState, setPanelState] = useState(currentState);

  

   const handleSummaryClick = (event)=> {
        panelStepClick({actions:summaryButtonAction, stepId:id});
   }

    const renderChildren = () => {
        //console.log("Panel Step : Rendering children");
        
        if (currentState === "open"){
            if (children){
            
                return (
                    <React.Fragment>
                        {children.map((value, index) => {
                            return (
                                <div key={index}>
                                    {renderComponentModel(value)}    
                                </div>
                            )
                        })}
                    </React.Fragment>
    
                )
        
            }
            return  (<React.Fragment></React.Fragment>)
        } else if (currentState === "completed"){
            return completedView();
        }else{
            return pendingView();
        }
        
        
    }

    const completedView = () => {
        //if summary is array or object
        if (typeof summary === "string"){
            return(
                <div className="panel-step-completed">
                    <div className="header">{label}</div>
                    <div className="panel-step-summary">
                        <button type="button" className="edit-button btn" onClick={handleSummaryClick}>Edit</button>
                        <div className="current-value">{summary}</div>
                    </div>
                </div>
    
            )
        }else if (summary.type && summary.type === "questionanswertemplate"){
            let data = summary.data;
            return(
                <React.Fragment>
                    <div className="panel-step-completed">
                        <div className="header">{label}</div>
                        <div className="panel-step-summary">
                            <button type="button" className="edit-button btn" onClick={handleSummaryClick}>Edit</button>
                            
                        </div>
                       
                    </div>
                    <NGISQuestionAnswerSummaryComponent data={data}></NGISQuestionAnswerSummaryComponent>
                </React.Fragment>
            
                
            )
        }


        

    };

    const pendingView = () => {

        return (
            <div className="panel-step-pending">
                <div className="header">{label}</div>
                
            </div>
        )
    }

    const getClassName = () => {

        switch (currentState){
            case "open":
                return "panel-step";
            case "completed":
                return "panel-step completed";
            case "pending":
                return "panel-step below";
            default:
                return "panel-step";
        }
    }

    let panelStepToShow = panelStepNumber;
    if (!(typeof displayedPanelStepId === "undefined" || displayedPanelStepId === null)){
        panelStepToShow = displayedPanelStepId;
    }


    return (
        <div className={getClassName()} data-number={panelStepToShow} >
            {renderChildren()}   
            <div className="panel-step-bottom"></div> 
            {rest.isLast && <span className="hide-signature"></span>}
        </div>
        
    )


}