import * as types from './ActionTypes'

export const setNGISRadioButtonSelectedValue = (id, selectedValue, selectedIndex) => ({
  type: types.NGIS_RADIOBUTTON_SELECTED,
  id : id,
  selectedValue : selectedValue,
  selectedIndex : selectedIndex
});

// This version is more composable from json
export const setNGISRadioButtonSelectedData = (actionData) => ({
  type: types.NGIS_RADIOBUTTON_SELECTED_DATA,
  id : actionData.id,
  selectedValue : actionData.selectedValue,
  selectedIndex : actionData.selectedIndex,
  condition : actionData.condition
});