import React, { Component, useState, useRef } from "react";
import RootComponentProvider from "../../componentProviders/RootComponentProvider";
import { flags } from "../../services/stateManagement/store";
import "./style.css";

export default function InputPage(props) {
  const [jsonInput, setJsonInput] = useState(null);
  const valueRef = useRef();

  const [visible, setVisible] = useState(false);

  console.log("Input page called");

  const handleSubmit = (event) => {
    flags.init = false;
    setVisible(false);
    setJsonInput(valueRef.current.value);
    event.preventDefault();
  };

  const showHide = (event) => {
    event.preventDefault();
    setVisible(!visible);
  };

  return (
    <div>
      <div className="template-selector">
        <a href="javascript:void(0)" onClick={showHide}>
          Select Template..
        </a>
        {visible && (
          <div>
            <button className="render-button" onClick={handleSubmit}>
              Render »
            </button>
            <textarea ref={valueRef}></textarea>
          </div>
        )}
      </div>

      <RootComponentProvider questionModelText={jsonInput} />
    </div>
  );
}
