
import {DATE_VALIDATION_MESSAGES} from './ValidationMessages';

export function validateDate(fullDate, mininumDate, customMessages) {
    let results = [];
    const day = fullDate.day;

    var requiredValidation = {...DATE_VALIDATION_MESSAGES.NO_DATE};

    if (customMessages.requiredMessage){
        requiredValidation.message = customMessages.requiredMessage
    }
   
    if (!fullDate.day && !fullDate.month && !fullDate.year) {
        results.push(requiredValidation);
        return results;
    }

    var dayValidationResult = validateDay(fullDate);
    var monthValidationResult = validateMonth(fullDate);
    var yearValidationResult = validateYear(fullDate);

    if (dayValidationResult.valueBlank && monthValidationResult.valueBlank && yearValidationResult.valueBlank) {
        
        results.push(requiredValidation);
        return results;
    } else {

        if (dayValidationResult.valueBlank) {
            results.push(DATE_VALIDATION_MESSAGES.DAY_REQUIRED);
        }
        if (monthValidationResult.valueBlank) {
            results.push(DATE_VALIDATION_MESSAGES.MONTH_REQUIRED);
        }
        if (yearValidationResult.valueBlank) {
            results.push(DATE_VALIDATION_MESSAGES.YEAR_REQUIRED);
        }
    }

    results = results.concat(dayValidationResult.messages);
    results = results.concat(monthValidationResult.messages);
    results = results.concat(yearValidationResult.messages);

    if (results.length === 0) {
        //check if date is valid
        const dayInt = parseInt(fullDate.day, 10);
        const monthInt = parseInt(fullDate.month, 10);
        const yearInt = parseInt(fullDate.year, 10);

        const isValidDate = isValidDay(dayInt, monthInt, yearInt);
        if (isValidDate === false) {
            results.push(DATE_VALIDATION_MESSAGES.INVALID_DATE);
        } else {

            //check date is not in future
            const isFutureDay = isFutureDate(dayInt, monthInt, yearInt);

            if (isFutureDay) {
                var validationMessage = {
                    key: DATE_VALIDATION_MESSAGES.FUTURE_DATE.key,
                    message: DATE_VALIDATION_MESSAGES.FUTURE_DATE.message
                }
                validationMessage.message = validationMessage.message + " " + getCurrentDate();
                results.push(validationMessage);
            } else if (mininumDate) {
                let parsedMinimumDate = /(\d{4})\-(\d{2})\-(\d{2})/.exec(mininumDate);
                let minDay = new Date(parsedMinimumDate[1], parsedMinimumDate[2], parsedMinimumDate[3], 0, 0, 0, 0);
                let inputDate = new Date(yearInt, monthInt, dayInt);

                if (inputDate < minDay) {
                    
                    var messageTemp = customMessages.minimumDateMessage;

                    if (!messageTemp){
                        messageTemp = DATE_VALIDATION_MESSAGES.MIN_DATE.message;
                    }

                    validationMessage = {
                        key: DATE_VALIDATION_MESSAGES.MIN_DATE.key,
                        message: messageTemp
                    }
                    let dobArray = mininumDate.split('-');
                    validationMessage.message = validationMessage.message + "(" + dobArray[2] + '/' + dobArray[1] + '/' + dobArray[0] + ')';
                    results.push(validationMessage);
                }
            }
        }
    }
    return results;
}



export function validateDay(fullDate) {
    let results = {
        valueBlank: true,
        messages: []
    }

    const day = fullDate.day;

    if (day === null || day === undefined) {
        return results;
    }

    if (!day.length) {
        return results;
    }

    var dayInt = parseInt(day, 10);

    if (!isNaN(dayInt) && !(dayInt > 0 && dayInt < 32)) {
        results.messages.push(DATE_VALIDATION_MESSAGES.INVALID_DAY);
    }

    results.valueBlank = false;

    return results;
}

function isTab(current, next) {

    if ((current === "day" || current === "month" || current === "year")
        && (next === "consent-day" || next === "consent-month" || next === "consent-year")) {
        return true;
    }
    return false;
}

export function validateMonth(fullDate) {
    let results = {
        valueBlank: true,
        messages: []
    }

    const month = fullDate.month;

    if (month === null || month === undefined) {
        return results;
    }

    if (!month.length) {
        return results;
    }

    var monthInt = parseInt(month, 10);
    if (!isNaN(monthInt) && !(monthInt > 0 && monthInt < 13)) {
        results.messages.push(DATE_VALIDATION_MESSAGES.INVALID_MONTH);
    }

    results.valueBlank = false;

    return results;
}

export function validateYear(fullDate) {
    let results = {
        valueBlank: true,
        messages: []
    }

    const year = fullDate.year;

    if (year === null || year === undefined) {
        return results;
    }

    if (!year.length) {
        return results;
    }

    var yearInt = parseInt(year, 10);

    if (isNaN(yearInt)) {
        results.messages.push(DATE_VALIDATION_MESSAGES.YEAR_REQUIRED);
        results.valueBlank = false;
        return results;

    }

    if (yearInt.toString().length < 4) {
        results.messages.push(DATE_VALIDATION_MESSAGES.INVALID_YEAR);
        results.valueBlank = false;
        return results;
    }

    if (yearInt < 1900) {
        results.messages.push(DATE_VALIDATION_MESSAGES.INVALID_YEAR_TOO_LOW);
        results.valueBlank = false;
    }

    results.valueBlank = false;

    return results;
}


export function isValidDay(dayInt, monthInt, yearInt) {
    if (yearInt < 1000 || yearInt > 3000 || monthInt === 0 || monthInt > 12)
        return false;

    var monthLength = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];

    // Adjust for leap years
    if (yearInt % 400 === 0 || (yearInt % 100 !== 0 && yearInt % 4 === 0))
        monthLength[1] = 29;

    // Check the range of the day
    return dayInt > 0 && dayInt <= monthLength[monthInt - 1];
}

export function isFutureDate(day, month, year) {
    const current = new Date();
    current.setHours(0, 0, 0, 0);
    const passedDate = new Date(year, month - 1, day, 0, 0, 0, 0);
    return passedDate > current;
}

export function getCurrentDate() {
    const d = new Date();
    const dateString = ("0" + d.getDate()).slice(-2) + "/" + ("0" + (d.getMonth() + 1)).slice(-2) + "/" +
        d.getFullYear();
    return dateString;
}