import React, { useState, useRef } from "react";
import renderComponentModel from '../../services/rendering/ComponentRenderer'
import iconWarning from './images/alert-icon-warning.svg';
import "./style.css";

const AlertComponent = (
{
    id,
    messages,
    alertType,
    ...rest
}) => {

   
   

    const renderChildren = () => {
        
        if (messages){
            
            return (
                <React.Fragment>
                    <ul className="message-list">

                   
                        {messages.map((message, index) => {
                            return (
                                <li className={getMessageLineClass()} key={index}>
                                    {message}
                                </li>
                                
                            )
                        })}
                     </ul>
                </React.Fragment>

            )
    
        }
        return  (<React.Fragment></React.Fragment>)
    }

    const getAlertContainerClass = () => {

        const baseCSS = "cs-alert-container";
        switch (alertType.toLowerCase()) {
            case "info":
                return baseCSS + " " + "cs-alert-info";               
            case "warning":
                return baseCSS + " " + "cs-alert-warning";
            case "success":
                return baseCSS + " " + "cs-alert-success";             
            case "error":
                return baseCSS + " " + "cs-alert-error"; 
            default:
                return baseCSS + " " + "cs-alert-info";    //default to info      
        }  
    }

    const getMessageLineClass = () => {
        const baseCSS = "message-line";
        switch (alertType.toLowerCase()) {
            case "info":
                return baseCSS + " " + "cs-message-line-info";               
            case "warning":
                return baseCSS + " " + "cs-message-line-warning";
            case "success":
                return baseCSS + " " + "cs-message-line-success";             
            case "error":
                return baseCSS + " " + "cs-message-line-error"; 
            default:
                return baseCSS + " " + "cs-message-line-info";    //default to info      
        }  

    }

    

    return (
        <div className={getAlertContainerClass()} data-testid='alert'>
            {renderChildren()}   
        </div>
        
    )


}

export default AlertComponent;