import React from "react";
import NGISTextInputComponent from './NGISTextInputComponent';
import {useSelector, useDispatch, AppStateContext} from '../../services/stateManagement/store'
import {updateComponentStateValueAction} from '../../actions/StateUpdateActions'

export const NGISTextInputContainer = (props) => {

    const required = props.required;
    const readOnly = props.readOnly;
    const maxLength = props.maxLength;
    const placeHolder = props.placeHolder;
    const orientation = props.labelOrientation;
    const infoText = props.infoText;

    const defaultValue = "";

    let currentValue = useSelector((state)=> {  
        if (state && state.questionnaire && state.questionnaire[props.id]){
            return state.questionnaire[props.id].value;
        }
        return defaultValue;
      
      });

    if (currentValue === null || typeof currentValue === "undefined"){
        currentValue = defaultValue;
    }
    const dispatch = useDispatch();


    const onChange = (event) => {
        event.preventDefault();
        dispatch(updateComponentStateValueAction({itemId:props.id, itemValue:event.target.value}));

    };

    return (

        <NGISTextInputComponent value={currentValue} 
            labelText={props.labelText} 
            orientation={props.labelOrientation} 
            disabled={props.readOnly} 
            placeholder={props.placeholder} 
            required={required} 
            maxChar={maxLength} 
            infoText={infoText} 
            onChange={onChange} 
            requiredIcon = {props.requiredIcon}
            >

        </NGISTextInputComponent>

    )
};