import { act } from "react-dom/test-utils";
import actionMapper from '../actions/ActionMapping'

export const dispatchAction = (actions, dispatch, isSingleDispatchAction) =>{

    if (isSingleDispatchAction === true){
        dispatch(actions);
  
      }else{

        actions.forEach(action => {
            dispatch(action);
          });
      }
}



export const getIsSingleDispatchFromProps = (props) => {

    let isSingleDispatch = false; //default to false;
    
    if (props.singleDispatchAction && props.singleDispatchAction === true){
      isSingleDispatch = true;
    }

    return isSingleDispatch;

}

export const composeActionsFromProps = (props) => {

  const actionList = [];
  props.actions.forEach(actionItem => {

    const actionData = actionItem.actionData;
    const actionToCall = actionMapper(actionItem.action);
    const otherAction = actionToCall(actionData);
    actionList.push(otherAction);

  });

  return actionList;

}
 const dispatchElementActions = (props, dispatch) => {

  const actionList = composeActionsFromProps(props);

  const isSingleDispatch = getIsSingleDispatchFromProps(props);

  dispatchAction(actionList, dispatch, isSingleDispatch );


}

export default dispatchElementActions