import React, { useState, useRef } from "react"
import {LabelComponent} from './LabelComponent'
import {useSelector} from '../../services/stateManagement/store'
import  {replaceOptions, replaceVariable} from '../../common/substituteVariables'

export const LabelContainer = (props) => {

    const labelText = replaceVariable(props.labelText, props);

    const defaultVisible = true;

    let visible = useSelector((state)=> {  
        if (state && state.questionnaire && state.questionnaire[props.id]){
            return state.questionnaire[props.id].visible;
        }
        return defaultVisible;
      
      });

    if (visible === null || typeof visible === "undefined"){
        visible = defaultVisible;
    }   

    if (!visible){
        
        return null;
    } 

    return (

        <LabelComponent labelText={labelText} bold={props.bold} visible={visible} labelStyle = {props.labelStyle}  />         
  )
};