import React, { useState, useRef, createRef } from "react";
import './style.css'
import UploadingFile from './UploadingFile.js';
import {fileUploadManager} from '../../services/fileUploadManager'
import NGISInfoMessageComponent from '../../questionnaireElements/NGIS-InfoMessage/NGISInfoMessageComponent'
import NGISFileUploadInfoMessage from './NGISFileUploadInfoMessage'

const NGISFileUploadComponent = ({
    id,
    uploadedFiles,
    disabled,
    comboBoxItems,
    onChange,
    dateInputData,
    fileTypeOptions,
    errorMessages,
  ...rest
}) => {

    const htmlInput = useRef(); 
   
    const [cbBoxItems] = useState(comboBoxItems);

    const handleChangeEvent = (changeEvent) => {
        // there to prevent react from complaining
        console.log(changeEvent.target.value);
    }

    let uploadContainerStyle = "upload-document-container";
    if (disabled === true){
      uploadContainerStyle = uploadContainerStyle + " upload-document-container-disabled";
    }

    console.log("Uploaded files count" + uploadedFiles.length);

    const showUploadFiles = () => {
      const usedFileTypes=[];
      
      return(
        uploadedFiles.map((file)=>{
          var fileInfo = fileUploadManager.getFileMetaData(file.displayName);
          
          var fileDate = file.fileDate;
          if (typeof fileDate === "undefined" || fileDate===null){
            fileDate = {year:"", month:"", day:""};
          }

          var filteredFileTypeOptions = filterFileTypes(fileTypeOptions, usedFileTypes);
          console.log("filtered file types " + JSON.stringify(filteredFileTypeOptions));

          if (file.fileType !== null && file.fileType !== undefined){
            usedFileTypes.push(file.fileType);
          }
          
          return (<UploadingFile name={file.displayName} 
            key={file.uniqueName} 
            uniqueName={file.uniqueName} 
            displayName={file.displayName} 
            fileMetaData={fileInfo.file} 
            loaded={file.loaded} 
            comboBoxItems={cbBoxItems} 
            onFileRemoved={handleRemoveFile} 
            dateInputData={dateInputData} 
            fileDate={fileDate} 
            fileType={file.fileType} 
            fileTypeOptions={filteredFileTypeOptions} 
            onChange={handleChange}/>)
          
        })
      )
    };

    const filterFileTypes = (items, inUse) => {
      return items.filter(item=>{
        var index = inUse.indexOf(item.value);
        if (index === -1){
          return item;
        }
        

      })
    }

    const handleRemoveFile = (displayName)=>{
      //pass on to container to modify state
      onChange({eventType:"file-removed", displayName: displayName});

    }

    const handleChange = (eventData)=>{
      //pass on to container to modify state
      if (eventData.eventType === "file-added"){
        onChange({eventType: eventData.eventType, files : [{fileName:eventData.fileName, displayName:eventData.displayName, loaded:true}]});
      } else if (eventData.eventType === "file-type-updated"){
        console.log("Component - On change " + eventData.fileType);
        onChange({eventType:eventData.eventType, fileName : eventData.fileName, displayName: eventData.displayName, fileType : eventData.fileType});
      } else if (eventData.eventType === "file-date-updated"){
        onChange({eventType : eventData.eventType, fileName : eventData.fileName, displayName: eventData.displayName, fileDate : eventData.fileDate});
      }
      
    }


    // TODO : use removeFile redux action to remove the file from store
  const removeFile = (fileName) => {
    //onRemove(fileName);
    
    //console.log('removing file ', fileName);
    //let sf = uploadedFiles;
    //let newFiles = [];
    //for (let i = 0; i < sf.length; i++) {
    //  if (sf[i].input !== fileName) {
    //    newFiles.push(sf[i]);
    //  }
    //}
    //setUploadedFiles(newFiles)
  }



  const onUpload = event => { 

    console.log("Upload2 called");
    let newFiles = event.target.files;
    let stateFiles = [...uploadedFiles];
    let hasErrors = false;
    for (let i = 0; i < newFiles.length; i++) {
      let hit = false;
      stateFiles.forEach((sf) => {
        if (sf.displayName === newFiles[i].name) {
          hit = true;
          console.log("File already exists");
        }
      })
      if ( !/jp(e)?g|png|pdf/.test(newFiles[i].type) ) { // Second line of file upload defense if <input accept={} /> is not supported
        //this.setState({ errorMessage : `Failed to upload ${newFiles[i].name}, unsupported file type "${newFiles[i].name.substring(newFiles[i].name.lastIndexOf("."))}"` });
        let errorText = `Failed to upload ${newFiles[i].name}, unsupported file type "${newFiles[i].name.substring(newFiles[i].name.lastIndexOf("."))}"`;
        console.error("Unsupported file type");
        onChange({eventType:"file-upload-error",error:[{messageType:"message-line", messageText:errorText}]});
        hasErrors = true;
        break;
      } else if ( !hit ) { // if the file has not already been uploaded
        const fileData1 = newFiles[i];
        // Enforce 10 MB maximum
        if(newFiles[i].size <= 10485760) {
          var fileInfo = {displayName:newFiles[i].name, uniqueName: Date.now() + newFiles[i].name, fileMetaData: newFiles[i], loaded : false};
          stateFiles.push(fileInfo);
          fileUploadManager.addFileMetaData(newFiles[i].name,fileData1);
          
        } else {
          let errorText = `Failed to upload ${newFiles[i].name}. Maximum file size is 10 MB.`;
          onChange({eventType:"file-upload-error",error:[{messageType:"message-line", messageText:errorText}]});
          console.error("Max size reached");
          hasErrors = true;
          break;
          
         }
      }
    }
    
      if (!hasErrors){
        onChange({eventType:"file-added",files:stateFiles});
      }
      
      htmlInput.current.value=null;
    }; 
    
  const hasUploads = uploadedFiles  && uploadedFiles.length > 0;

  const fileWarningMessage = [
    {messageType:"message-line", messageText:"Please make sure you have uploaded all required forms (child assent, consultee, etc.), you currently have uploaded the files..."},
  ];

  if (uploadedFiles){
    uploadedFiles.forEach(item=>{
      fileWarningMessage.push({messageType:"message-bullet", messageText : item.uniqueName});
    });
    
  }

  const totalUsedFileTypes=[];
      
  if (uploadedFiles) {
    uploadedFiles.map((file)=>{
        if (file.fileType !== null && file.fileType !== undefined){
          totalUsedFileTypes.push(file.fileType);
        }
      })
  }

  const maxFilesUploaded = fileTypeOptions && totalUsedFileTypes && fileTypeOptions.length === totalUsedFileTypes.length;

  const fileInfoMessage = [
      {messageType:"message-line", messageText:"The maximum number of form types has been reached."},
    ];

  const hasErrorMessages = errorMessages.length > 0;

  const getErrorMessages = () => {

    return(
      <div className='file-upload-error-container'>
         <NGISFileUploadInfoMessage alertType="error" messages={errorMessages}></NGISFileUploadInfoMessage>
      
      </div>
     
    )

  };
    

  return (
    <div className='upload-document-padding'>
        <label className='form-label'>Attach All scanned forms</label>
        {hasErrorMessages && getErrorMessages()}
        <div className={uploadContainerStyle}>
          <input disabled={disabled} type='file' id='upload_doc' className="invisibility-cloak" 
          ref={htmlInput} onChange={onUpload} multiple />
          <label disabled={disabled} htmlFor='upload_doc' className="upload-document-button"> Upload </label>
          <p className='upload-document-types'> PDF, JPG, PNG Maximum 10MB </p>
          <NGISInfoMessageComponent messageType="Blue" textMessage="Your file may take a minute to upload, depending on its size. You can continue to fill out this form, but will not be able to complete and submit it until the upload has completed."/>
        </div>
        {showUploadFiles()}
        {hasUploads && <NGISFileUploadInfoMessage alertType="warning" messages={fileWarningMessage}> </NGISFileUploadInfoMessage>}
        {maxFilesUploaded && <NGISFileUploadInfoMessage alertType="warning" messages={fileInfoMessage}> </NGISFileUploadInfoMessage>}
      </div>
  )
};

export default NGISFileUploadComponent;