import React, { useState, useRef } from "react";

import "./style.css";

const RadioButtonComponent = ({
  labelText,
  orientation,
  disabled,
  required,
  options,
  onChange: notify,
  ...rest
}) => {
  const radioButtonRef = useRef(null);

  let isInline = false;

  const parseOrientation = (orientationText) => {
    try {
      const temp = orientationText.toLowerCase();
      if (temp === "inline") {

        return true;
      } else if (temp === "top") {
        return false;
      }
    } catch (e) {
      console.log(e);
      throw 'Unable to parse orientation';

    }
  }

  if (orientation !== null && orientation !== undefined) {
    isInline = parseOrientation(orientation);
  }

  
  const getRadioButtonContainerClass = () => {

    if (isInline) {
      return 'radio-button-container-horizontal';
    } else {
      return 'radio-button-container-vertical';
    }

  };


    return (
        <div className="radio-questions">
            <div className='question-title'>
                <h5 className="d-inline">{labelText}</h5>
            </div>

            {options !== undefined && options.map((option, index) => {
                return (
                    <div className={getRadioButtonContainerClass()} key={index}>
                    <input type="radio" name="test" value={option} ref={radioButtonRef} aria-checked="false" id={index} checked={option} />
                    <label key={index} id={option + "-" + index} >
                        {option}
                    </label>
                    </div>
                )
                }
            )}
        </div>
      )
};

export default RadioButtonComponent;