import React from 'react';
import {NGISFileUploadContainer} from './NGISFileUploadContainer';


export function NGISFileUploadProvider (item){
    return(
        <NGISFileUploadContainer {...item}/>
    )

};

export function NGISFileUpdateStateInitializer(item){
    
    const initialState = {
        uploadedFiles : [],
        isValid : false,
        maxFormsReached : false,
        errorMessages : []
    };

    return initialState;



}



 