import React, { useState, useRef, useEffect } from "react";
import eventManager from "../../services/events/eventManager";
import renderComponentModel from '../../services/rendering/ComponentRenderer'
import {eventTypes} from '../../services/events/eventAndActionTypes'
import "./style.css";

export const NGISPanelComponent = (
{
    id,
    panelSteps,
    current,
    panelVisibilityList,
    ...rest
}) => {

    if (current === null || current === undefined){
        current = panelVisibilityList[0];
    }

    const currentStep = current;

    let panelStepNumber = 0;

    
    // const [currentStep, setCurrentStep] = useState(current);

    // const handleEvent = (eventData) => {

    //     console.log("PANEL RECEIVED : " + JSON.stringify(eventData));
       
    //     if (eventData.action === 'ngis-panel/move-next'){
    //         if (currentStep < panelSteps.length-1){
    //             var nextStep = currentStep+1;
    //             setCurrentStep(nextStep);
    //         }
    //     } else if (eventData.action === 'ngis-panel/move-to-panel-step'){
    //         return;
    //         const newStep = eventData.data;
    //         setCurrentStep(newStep);
    //     }
        
    // }

    // useEffect(() => {
    //     const subscripton = eventManager.subscribe(eventTypes.CLICK, handleEvent );
    //     // Specify how to clean up after this effect:
    //     return function cleanup() {
    //         subscripton.dispose();
    //     };
    // });

    //register to events

    const renderChildren = () => {
        
        if (panelSteps){
            console.log('rendering panel steps again. Current step= ' + currentStep);
            console.log("Panel visibility list " + JSON.stringify(panelVisibilityList));

            const visiblePanelList = [];
            panelSteps.map((value, index) =>{
                var item = panelVisibilityList.find(f=>f.id==value.id);

                if (typeof item !== "undefined" && item.visible === true){
                    visiblePanelList.push(value.id);
                }
            });


            return (
                <React.Fragment>
                    {panelSteps.map((value, index) => {

                        var indexOfItemInVisibleList = visiblePanelList.indexOf(value.id);

                        var isVisible = indexOfItemInVisibleList > -1;

                        var panelState = value.id === current ? "open" : "pending";

                        //var panelState = currentStep === index ? "open" : "pending";

                        if (isVisible){

                            var isLast = indexOfItemInVisibleList === visiblePanelList.length-1;

                            if (indexOfItemInVisibleList < visiblePanelList.indexOf(current)){
                                panelState = "completed";    
                            }
                            panelStepNumber = panelStepNumber+1;
                            //console.log("Index="+index + " state="+panelState);
                            return (
                                <div key={index}>
                                    {renderComponentModel(value, index, {"panelState": panelState, "panelIndex": index, "isLast" : isLast, "parentPanelId": id, "panelStepNumber": panelStepNumber})}                                 
                                </div>
                            )
                            
                        }

                        
                    })}
                </React.Fragment>

            )
    
        }
        return  (<React.Fragment></React.Fragment>)
    }

    

    return (
        <div className="steps-list">
            {renderChildren()}   
        </div>
        
    )


}