import React, { useState, useRef } from "react";
import SignatureComponent from './SignatureComponent';
import {useSelector, useDispatch} from '../../services/stateManagement/store'
import {updateComponentStateValueAction} from '../../actions/StateUpdateActions'

export const SignatureContainer = (props) => {

    const dispatch = useDispatch();

    let defaultSignature = undefined;

  
    let signatureData = useSelector((state)=> {  
        if (state && state.questionnaire && state.questionnaire[props.id]){
            return state.questionnaire[props.id].value;
        }
        return defaultSignature;
      
      });    
  

    const updateSignature = (eventData)=>{

        console.log("Called");
        
        dispatch(updateComponentStateValueAction({itemId:props.id, itemValue:eventData.data}));

    }

    const defaultVisible = true;

    let visible = useSelector((state)=> {  
        if (state && state.questionnaire && state.questionnaire[props.id]){
            return state.questionnaire[props.id].visible;
        }
        return defaultVisible;
      
      });

    if (visible === null || typeof visible === "undefined"){
        visible = defaultVisible;
    }   

    if (!visible){
        
        return null;
    } 

    return (

        <SignatureComponent labelText={props.labelText} updateSignaureCallback={updateSignature} visible={visible} signatureData={signatureData}></SignatureComponent>
       

       
  )
};