import React, { useState, useRef, Component } from "react";
import renderComponentModel from '../../services/rendering/ComponentRenderer'

import "./style.css";

export class CollapsibleLinkComponent extends Component {

    constructor(props){
        super(props);
        this.handleClick = this.handleClick.bind(this);
        this.state ={isCollapsed:true}
    }
    
   handleClick = () => {
        const temp = this.state.isCollapsed;
        if(temp){
            this.setState({isCollapsed:false});
        }else{
           this.setState({isCollapsed:true});
        }
       
    }

    renderLink() {
        if(this.state.isCollapsed){
            return(
                <div className="collapsible-link">
                    <a className="collapse-show-hide" onClick={this.handleClick} >{this.props.openMessage}</a>
                </div>

            )
        }
        else{
            console.log("entered else");
            return(
                <div>
                    <div className="collapsible-link"> 
                        <div>
                            <a className="collapse-show-hide" onClick={this.handleClick} >{this.props.closeMessage}</a>
                        </div>
                        
                        <div>{this.renderChildren()}</div>
                    </div>
                </div>
                
                
            )
        }
        
    }

    renderChildren = () => {
        if (this.props.children){
            
            return (
                <React.Fragment>
                    {this.props.children.map((value, index) => {
                        return (
                            <div className="collapsible-link-item" key={index}>
                                {renderComponentModel(value)}    
                            </div>
                        )
                    })}
                </React.Fragment>

            )
    
        }
        return  (<React.Fragment></React.Fragment>)
    }


    render(){
        return(
            <div>{this.renderLink()}</div>
        )
    }
       

}


