import * as types from '../actions/ActionTypes'
import produce from 'immer'
import { stateProvider} from '../services/stateManagement/stateProvider'

export default function questionnaireReducer(state={}, action){

    const nextState = produce(state, draft => {

        switch (action.type){

            case types.APP_INIT_STATE:
                //console.log("questionnaire reducer draft= " + JSON.stringify(draft));
                //console.log("questionnaire reducer payload= " + JSON.stringify(action.payLoad));
                draft.questionnaire = action.payLoad.questionnaire;
                break;

            default:
                break;
        }



    });
    //console.log("init newState = " + JSON.stringify(nextState));
    //TODO: find better way to update state 
    stateProvider.current = nextState;
    return nextState;
    

}

