import * as types from '../actions/ActionTypes'
import produce from 'immer'
import ConditionalEvaluator from '../common/ConditionalEvaluator'


export default function updateComponentStateValueReducer(state, action) {

    const nextState = produce(state, draft => {

        switch (action.type) {

            case types.UPDATE_COMPONENT_STATE_VALUE: {

                const id = action.id;
                draft.questionnaire[id].value = action.value;

                break;
            }
            case types.UPDATE_COMPONENT_STATE_PROPERTY: {

                const actionData = action.data;

                actionData.forEach(actionElement => {
                    var canUpdate = checkCondition(actionElement.condition, state);
                    if (canUpdate === true) {
                        let id = actionElement.id;
                        let property = actionElement.property;
                        let value = actionElement.value;
                        draft.questionnaire[id][property] = value;
                    }
                });

                break;
            }

            default:
                break;
        }
    });

    return nextState;


}

const checkCondition = (condition, state)=>{
    if (typeof condition === "undefined" || condition === null){
        return true;
    }

    if (Array.isArray(condition) && condition.length === 0){
        return true;
    }
    return ConditionalEvaluator.evaluate(condition, null, state);
}