import * as types from './ActionTypes'

export const clearValuesAction = (data) => ({
    type: types.QUESTIONNAIRE_CLEAR_VALUES,
    ids : data.items
   
  });

  export const clearValidationResult = (data) => ({
    type: types.QUESTIONNAIRE_CLEAR_VALIDATION_RESULT,
    ids : data.items

  });

  export const clearEnsusingValuesAction = (data)=>({
    type: types.QUESTIONNAIRE_CLEAR_ENSUING_VALUES,
    id: data.id

  });