import React, { useState, useRef } from "react";
import {DateInputComponent} from './DateInputComponent';
import {useSelector, useDispatch, AppStateContext} from '../../services/stateManagement/store'
import {updateComponentStateValueAction} from '../../actions/StateUpdateActions'

export const DateInputContainer = (props) => {
    
  const defaultValue = {year:"", month:"", day:"", isValid:false};

  let currentValue = useSelector((state)=> {  
        if (state && state.questionnaire && state.questionnaire[props.id] && state.questionnaire[props.id].value){
            return state.questionnaire[props.id].value;
        }
        return defaultValue;
      
  });

  if (!isDefined(currentValue.year)){
    currentValue.year = defaultValue.year;
  }
  if (!isDefined(currentValue.month)){
    currentValue.month = defaultValue.month;
  }
  if (!isDefined(currentValue.day)){
    currentValue.day = defaultValue.day;
  }

  const defaultDisabled = props.disabled === true ? props.disabled : false;

  let disabled = useSelector((state)=> {  
    if (state && state.questionnaire && state.questionnaire[props.id] && state.questionnaire[props.id].disabled){
        return state.questionnaire[props.id].disabled;
    }
    return defaultDisabled;
  
  });

  const customMessages = {
    requiredMessage : props.requiredMessage,
    minimumDateMessage : props.minimumDateMessage,
  }

  const dispatch = useDispatch();


  const onChange = (eventData) => {
    dispatch(updateComponentStateValueAction({itemId:props.id, itemValue:eventData}));

  };


  return (
    <DateInputComponent labelText={props.labelText} minDate={props.minimumDate} disabled={disabled} onChange={onChange} currentValue={currentValue} customMessages={customMessages} ></DateInputComponent>
  )
};

const isDefined = (value)=>{
  if (value === undefined || value === null){
    return false;
  }
  return true;
}