import React, { Component, useState, useEffect } from 'react';
import hash from 'object-hash';
import Dropdown from './Dropdown'

import { DateInputComponent } from '../DateInput/DateInputComponent';
import {fileUploadManager} from '../../services/fileUploadManager'
import NGISSelectDropDownComponent from '../NGIS-SelectDropdown/NGISSelectDropdownComponent'

const UploadingFile = ({
  name,
  displayName,
  uniqueName,
  loaded,
  fileMetaData,
  comboBoxItems,
  onFileRemoved,
  onChange,
  dateInputData,
  fileDate,
  fileType,
  fileTypeOptions,
}) => {

  console.log("In Uploading file");
  console.warn("In Upload file type " + JSON.stringify(fileType));
  console.warn("In Upload file type options" + JSON.stringify(fileTypeOptions));

  const [bufferedBytes, setBufferedBytes] = useState(0);
  const [totalBytes, setTotalBytes] = useState(0);

  const [hash, setHash] = useState(null);
  const [data, setData] = useState(null);
  const [isDisabled, setIsDisabled] = useState(true);

  const [fileRemoved, setFileRemoved] = useState(false);

  useEffect(() => {
    if (!loaded){
      readFile(fileMetaData);
    }
  },[])  

  const addToUploaded = (displayName, uniqueName, hash, size, data) => {
 
    fileUploadManager.addFile(name, uniqueName, data, hash);
    onChange({eventType:"file-added", fileName:uniqueName, displayName: displayName});
    //now set file loaded
    console.log("Onchange add to upload called" + JSON.stringify(name));

  }

  const readFile = (file) => {
    let fr = new FileReader();
    // add the file readers event callbacks
    fr.onprogress = (e) => {
      setTotalBytes(file.size);
      setBufferedBytes(e.loaded);
      console.log("progress called");
    };
    fr.onload = () => {
      let fileData = fr.result;
      console.log("file details :: " + totalBytes + " BB  :: " + bufferedBytes);
      setBufferedBytes(totalBytes);
      //setData(fileData);
      //setHash(hash.sha1(name));
      //addToUploaded(displayName, uniqueName, hash.sha1(displayName), totalBytes, fileData);

      addToUploaded(displayName, uniqueName, "some crazy hash", totalBytes, fileData);
    };
    if (!file){
      console.error("File metadata is null");
    }
     fr.readAsDataURL(file); // start reading the file
    
  }


  const getUploadBar = () => {
    
    return (
      <div className="progress">
        <div className="determinate" style={{ width: (bufferedBytes / totalBytes) * 100 + '%' }}></div>
      </div>
    )
  }

  

  const getLoadState = () => {
    if (!loaded){
      if (bufferedBytes !== totalBytes) {
        return `${returnFileSize(bufferedBytes)} of ${returnFileSize(totalBytes)}`;
      } else {
        return `Successfully Uploaded`;
      }

    }else{
      return `Successfully Uploaded`;
    }
    
    
  }

  
  const returnFileSize = (bytes) => {
    if (bytes < 1024) {
      return bytes + 'bytes';
    } else if (bytes >= 1024 && bytes < 1048576) {
      return (bytes / 1024).toFixed(1) + 'KB';
    } else if (bytes >= 1048576) {
      return (bytes / 1048576).toFixed(1) + 'MB';
    }
  }

  const handleDropDownChange = (eventData) => {
    console.log("Handle file change called");
    onChange({eventType:"file-type-updated", fileType: eventData.selectedValue, displayName: displayName});
    //call onChange
  }

  const handleDateTimeChange = (eventData)=>{
    console.log("Handle date change called");
    onChange({eventType:"file-date-updated", fileDate: eventData, displayName: displayName});
  }

  const handleRemoveFile = (eventData)=>{
    
    fileUploadManager.removeFile(displayName);
    onFileRemoved(displayName);
  }


const customDateMessage = {requiredMessage : dateInputData.requireDateMessage, minimumDateMessage : dateInputData.requiredDateMessage};

const dateComponentDisabled = fileType === null || fileType === undefined || fileType==="";


  
    return (
      <div id={name} className="uploading-file-container">
        <div className="row">
          <div className="Progress-bar">
            <p className='uploaded-filename' >{displayName}</p>
            <p className='loading-data-count'>{getLoadState()}</p>
            <img className='cancel-logo' alt='cancel by hans draiman from the Noun Project' src={require('../../assets/img/cancel-logo.png')}
              onClick={(e) => handleRemoveFile(e)} /> 
            {getUploadBar()}
          </div>
            <div className="file-upload-combo-box">              
                {/* <Dropdown dropDownItems={comboBoxItems} onDropdownChange={handleDropDownChange} setIsDisabled={setIsDisabled}/>    */}
              <NGISSelectDropDownComponent dropDownItems={fileTypeOptions} selectedValue={fileType} onChange={handleDropDownChange}></NGISSelectDropDownComponent>
                                 
            </div>     
          
          <div className="file-upload-signature-date">           
            <DateInputComponent labelText={dateInputData.dateInputLabel} 
              minDate={dateInputData.minimumDate} 
              disabled={dateComponentDisabled}
              onChange={handleDateTimeChange} 
              currentValue={fileDate} 
              customMessages={customDateMessage}>
            </DateInputComponent>
          </div>
        </div>
      </div>
    )
};



export default UploadingFile;
