import React, { useState, useRef } from "react";
import renderComponentModel from '../../services/rendering/ComponentRenderer'
import AlertComponent from '../Alert/AlertComponent'
import "./style.css";

const ValidationSummaryComponent = (
{
    id,
    validationGroupId,
    visible,
    messages,
    ...rest
}) => {


    
    
    if (!visible){
        return null;
    }

    const alertId = id + "-alert";

    return (
        <AlertComponent id={alertId} alertType="error" messages={messages} visible={visible} ></AlertComponent>
        
    )


}

export default ValidationSummaryComponent;