import React, { useState, useRef, useEffect } from "react";
import {componentProviderMapping} from '../componentProviderMapping';


export default function renderComponentModel (item, index, options){

    const componentType = item.type.toLowerCase();

    //console.log("item : " + JSON.stringify(item));

    const provider = componentProviderMapping[componentType];

    console.log("Rendering : " + componentType);

    if (!provider){
        throw ("No component found for provider " + componentType);
    }

    if (index !== null || index !== undefined){
 
        return (
            <React.Fragment key={index}>
                {provider(item, options)}
            </React.Fragment>
        )

    }else{
        return (
            <React.Fragment>
                {provider(item, options)}
            </React.Fragment>
        )

    }

    


}

