import substituteVariable from '../common/substituteVariables'

export const processTemplate = (templateItem, currentState, newState) =>{

    let template = templateItem.template;

    return processTemplateText(template, currentState );
}

export const processTemplateText = (template, currentState) =>{

    const regexpression  = /@@[a-zA-Z0-9:-]+/gm;

    var replacements = template.map(item=>{

        var temp = item;
        var tokens = item.match(regexpression);

        tokens && tokens.forEach(tokenItem => {
            var result = getTokenValue(tokenItem, currentState);

            if (result != null || typeof result !== "undefined"){
                temp = temp.replace(tokenItem, result);
            }
        });

        return temp;

    })
    
    return replacements;


}

const getTokenValue = (token, currentState) => {

    var items = token.replace("@@","").split(":");

    var itemId=items[0]; //this will be the element id
    var property1 = items[1];
    var property2 = null;

    if (items.length > 2){
        property2 = items[2];
    }

    //currently only radio button have property2
    try{
        if (property2 == null){
            return currentState.questionnaire[itemId][property1];
        }else{
            return currentState.questionnaire[itemId][property1][property2];
        }
        
    }catch (e){
        console.warn("Unable to get value for template." + e);
    }

    return null;
}

export const processQuestionAnswerTemplate = (templates, currentState) => {

    
    var result = templates.map((templateItem) => {
        var answer = null;

        var question = substituteVariable(templateItem.question);

       
        
        try{
            var answer = processTemplateText([templateItem.answer], currentState)[0];
            if (typeof answer === "string" && answer.indexOf("$")>-1){
                answer = substituteVariable(answer);
            }
        }catch (e){
            console.log("No answer found for " + templateItem.answer + " possible no answer.");
        }

        return {"question":question, "answer" : answer};
        

        
    }).filter(item=>(item.answer !== "null"));

    console.log(JSON.stringify(result));
    return result;

}
