import { sequentialBatchActions } from "../actions/BatchedActions"
import { stateProvider } from "../services/stateManagement/stateProvider"
import  canRunIf from "../common/processRunIfCondition"

export const combineReducers = (reducers)=> {
  // First get an array with all the keys of the reducers (the reducer names)
  const reducerKeys = Object.keys(reducers);

  return function combination(state = {}, action) {
    // This is the object we are going to return.
    const nextState = {}

    // Loop through all the reducer keys
    for (let i = 0; i < reducerKeys.length; i++) {
      // Get the current key name
      const key = reducerKeys[i];
      // Get the current reducer
      const reducer = reducers[key];
      // Get the the previous state
      const previousStateForKey = state[key];
      // Get the next state by running the reducer
      const nextStateForKey = reducer(previousStateForKey, action);
      // Update the new state for the current reducer
      nextState[key] = nextStateForKey;
    }
    return nextState;
  }
}

 const combineReducersUsingGlobalState = (reducers)=> {
    // First get an array with all the keys of the reducers (the reducer names)
    const reducerKeys = Object.keys(reducers);
  
    return function combination(state={}, action) {
      // This is the object we are going to return.
      let nextState = {};

      if (!Array.isArray(action)){
        //for not batch action evaluate now
        const canRunAction = canRunIf(action, state);
        if (!canRunAction){
          return state;
        }

      }
  
      // Loop through all the reducer keys
      for (let i = 0; i < reducerKeys.length; i++) {
        // Get the current key name
        const key = reducerKeys[i];

        let stateKey = key;

        // Get the current reducer
        const reducer = reducers[key];
        // Get the the previous state

        const previousStateForKey = i === 0 ? state : nextState;

        let nextStateForKey ;

        const actionContext = [];

        if (key === "eventsReducer"){
          actionContext.push("eventsReducer");
        }

        //console.log("In reducer " + key + " with action context " + JSON.stringify(actionContext));

        if (Array.isArray(action)){
          //batch action
          nextStateForKey = runBatchAction(previousStateForKey, reducer, action, actionContext);
          nextState = nextStateForKey;
        }else{
          action.actionContext = actionContext;     
          nextStateForKey = reducer(previousStateForKey, action);
          nextState = nextStateForKey;
        }

        

        //console.log("Looking at next state : " + JSON.stringify(nextState));
      }
      
      return nextState;
    }
  }

  const runBatchAction = (state, reducer, actions, actionContext) => {
    let nextState = {};
    let intermidiateState;

    actions.forEach((actionItem, index) => {
      //console.log("Running batch action " + actionItem.type + " action context " + JSON.stringify(actionContext));
      actionItem.actionContext = actionContext;
      const prevState = index === 0? state : nextState;
      let canRunAction = canRunIf(actionItem, prevState);

      if (canRunAction){
        intermidiateState = reducer(prevState, actionItem);
        nextState = intermidiateState;
      }else {
        console.warn("Can run action is false");
        nextState = prevState;
      }
     
    });

    return nextState;
  }

