
import Utils from '../common/HelperFunctions';

const extractValidationGroupResult = (validationGroupId, property, currentState) => {

    const questionnaireState =  currentState.questionnaire;

    let value = null;

    if (!Utils.propertyExistAndHasValue(questionnaireState, "_validationResult")){
        return value;
    }

    if (!Utils.propertyExistAndHasValue(questionnaireState._validationResult, validationGroupId)){
        return value;
    }

    if (!Utils.propertyExistAndHasValue(questionnaireState._validationResult[validationGroupId], property)){
        return value;
    }

    let validationGroup = questionnaireState._validationResult[validationGroupId];
 
    value =  validationGroup[property];

    if (typeof value === "undefined" || value === undefined){
        value = null;
    }

    console.log("Validation group value " + value);  
     return value;
 }

 export default extractValidationGroupResult;