import React, { useState, useRef } from "react";
import NGISRadioButtonComponent from './NGISRadioButtonComponent';
import {useSelector, useDispatch, AppStateContext} from '../../services/stateManagement/store'
 
import actionMapper from '../../actions/ActionMapping'
import {setNGISRadioButtonSelectedValue} from '../../actions/NgisRadioButtonActions'
import dispatchElementActions, {dispatchAction, getIsSingleDispatchFromProps} from '../../common/actionHelpers'
import  {replaceOptions, replaceVariable} from '../../common/substituteVariables'

export const NGISRadioButtonContainer = (props) => {


    const required = props.required;
    const orientation = props.optionsOrientation;

    const dispatch = useDispatch();

    const defaultVisible = true;

    let visible = useSelector((state)=> {  
        if (state && state.questionnaire && state.questionnaire[props.id]){
            return state.questionnaire[props.id].visible;
        }
        return defaultVisible;
      
      });

    if (visible === null || typeof visible === "undefined"){
        visible = defaultVisible;
    }

    let currentValue = useSelector((state)=> {  
        if (state && state.questionnaire && state.questionnaire[props.id] && state.questionnaire[props.id].value){
            
            if (typeof state.questionnaire[props.id].value.selectedValue === "string"){
                return state.questionnaire[props.id].value.selectedValue;
            }
            return null;
            
        }
        return null;
    
    });

    console.log("NGIS Radio current value" + currentValue);

    const initialValue = currentValue !== null ? currentValue : props.initialValue;



    const labelText = replaceVariable(props.labelText, props);

    const options = replaceOptions(props.options, props);

    


    const onClicked = (data) => {
        
        //we saved the selected item before any other actions.

        const defaultAction = setNGISRadioButtonSelectedValue(props.id, data.selectedValue, data.selectedIndex);
        const actionList= [defaultAction];

        const separateDispatch = [];

        if (typeof props.actions !== 'undefined'){
            props.actions.map((actionItem) => {
                
                const actionData = actionItem.actionData;
                const actionToCall = actionMapper(actionItem.action);
                const actionFunction = actionToCall(actionData);
                if (actionItem.separateDispatch === true){
                    console.warn("Separate dispatch");
                    separateDispatch.push(actionFunction);
                }else{
                    actionList.push(actionFunction);
                }
                

            });

        }

        const isSingleDispatch = getIsSingleDispatchFromProps(props);

        dispatchAction(actionList, dispatch, isSingleDispatch);

        separateDispatch.forEach(action=>{
            dispatch(action);
        });
    };

    if (!visible){
        return null;
    }

 
    return (

        <NGISRadioButtonComponent labelText={labelText}
            id = {props.id}
            orientation={orientation}
            disabled={props.readOnly}
            required={required}
            options={options}
            initialValue = {initialValue}
            actions = {props.actions}
            onClicked = {onClicked}
            >
        </NGISRadioButtonComponent>
  )
};

