import {stateInitializerMapping} from '../../services/stateInitializerMapper'
import {questionnaireInputData} from '../../services/questionnaireInputData'



const inputPreprocessor = (jsonText) => {

  try{
    
    if (typeof jsonText === "undefined"){
        return [null,null];
    }
    
    var inputData = JSON.parse(jsonText);

    const [elementModel, normalizedInputData, elementsInOrder] = generateElementModel(inputData);

    questionnaireInputData.normalized = normalizedInputData;
    questionnaireInputData.raw = inputData;
    questionnaireInputData.ordered = elementsInOrder;

    console.log("Initial generated State="+ JSON.stringify(elementModel));
    
   return [inputData, elementModel];
  }catch (e){ 
      console.error(e);
      return [null, null];
  }
     

}

const generateElementModel = (inputData) => {

    const state = {questionnaire:{
        _validationResult : {},
        _globalVariables : {}
    }};

    const normalizedInputData = {};

    const orderedElements = [];

    const idDict = {};

    if (inputData === null){
        return [state, normalizedInputData, orderedElements];
    }


    const getIds = (value, parentId) => {

        let id = value.id;
        
        if (typeof id === "undefined"){
            throw new Error ("No id defined for component " + JSON.stringify(value));
        }

        if (typeof idDict[id] !== "undefined"){
            throw new Error ("Id " + id + " already exists");
        }

        idDict[id]=id;

        orderedElements.push({id:id, type:value.type});

        const initialState = generateInitialState(value);

        state.questionnaire[id] = initialState;
        if (typeof parentId !== "undefined"){
            state.questionnaire[id].parentId = parentId; 
        }

        normalizedInputData[id] = value;

        if (typeof parentId !== "undefined"){
            state.questionnaire[id].parentId = parentId; 
            normalizedInputData[id].parentId = parentId;
        }

        if (typeof value.panelsteps !== "undefined"){
            
            value.panelsteps.map( (current, index)=> {

                getIds(current, id);
            });
        }

        if (typeof value.item !== "undefined"){
            
            value.item.map( (current, index)=> {

                getIds(current);
            });
        }
    }

    inputData.item.forEach(element => {
        getIds(element);
    });

    

    return [state, normalizedInputData, orderedElements];

}

const generateInitialState = (item) => {

   const id = item.id;
   const componentType = item.type.toLowerCase();

   const elementInitialStateProvider = stateInitializerMapping[componentType];

   if (typeof elementInitialStateProvider === "undefined"){
       return {};
   }
    
   var initialState = elementInitialStateProvider(item);

   return initialState;

}

export default inputPreprocessor;


