import React, { useState } from 'react';
import './style.css';


const NGISDropDownComponent = ({
    dropDownItems,
    placeholder,
    selectedValue,
    onChange,
    isDisabled
}) => {

    if (typeof placeholder === "undefined"){
        placeholder = {value:"", name:"Please Select.."}
    }

    var dropDownItemsInternal = [placeholder,...dropDownItems];

    console.log("Drop down = " + JSON.stringify(dropDownItemsInternal));
    
    const [selected, setSelected] = useState(selectedValue);
    //const [selectedObject, setSelectedObject] = useState(null)

   

    

    const handleChange = (event) => {
        console.log("Handle change called");

    }




    return (
        <select className="select-css" value={selected} onChange={handleChange}>
            {dropDownItemsInternal.map((item, index) => (
             <option key={index} value={item.value}>{item.name}</option>
            ))}
            
          </select>
        
       
    )
};

export default NGISDropDownComponent
