

import ConditionalEvaluator from './ConditionalEvaluator';


const canRunIf = (actionInfo, currentState, newState) => {
  
    let canRun = true;
    //batched action
    if (Array.isArray(actionInfo)){
        for (let index = 0; index < actionInfo.length; index++) {
            const action = actionInfo[index];

            canRun = canRunAction(action, currentState, newState);

            if (!canRun){
                break;
            }
            
        }
        return canRun;
    }else{

        return canRunAction(actionInfo, currentState, newState);
    }
    
    

}

const canRunAction = (action, currentState, newState) => {
    let canRun = true; //default

    if (action && action.runIf){

        const runIf = action.runIf;

        canRun = ConditionalEvaluator.evaluate(runIf, null, currentState);

        //console.log("Evaluated can run. Value = " + canRun);
        
    }

    return canRun;



}

export default canRunIf;