export const propertyExistAndHasValue = (object, property) => {

    if (object === null || object === undefined){
        return false; 
    }

    if (property in object){

        if (object[property] !== null || object[property] !== undefined){
            return true;
        }
    }

    return false;
}

 const compareOperation = (source, newValue, operator) => {

    switch (operator){
        case "=": {
            //console.log("Comparing source +" + source + " newValue" + newValue + " operator" + operator);
            return source == newValue;
        }
        case "!=" :{
            return source != newValue;
        }
        case "contains" :{
            if (!Array.isArray(newValue)){
                return false;
            }
            return newValue.indexOf(source) > -1;
        }
        case "length=":{
            if (!Array.isArray(source)){
                return false;
            }
            var length= source.length;
            return length == newValue;
        }
        case "length!=":{
            if (!Array.isArray(source)){
                return false;
            }
            var length= source.length;
            return length != newValue;
        }
        case "notnull":{
            return !(source === null || source === undefined);
        }
        case "null":{
            return (source === null || source === undefined);
        }
        break;
        default:
            throw 'Not supported operator ' + operator;
    }

    return false;
}

const unfreeze=(o)=>{
    var oo=undefined;
     if( o instanceof Array){
             oo=[];var clone=function(v){oo.push(v)};
             o.forEach(clone); 
     }else if(o instanceof String){
        oo=new String(o).toString();
   }else  if(typeof o =='object'){

      oo={};
     for (var property in o){oo[property] = o[property];}


     }
     return oo;
}

const Utils ={

    propertyExistAndHasValue : propertyExistAndHasValue,
    compareOperation : compareOperation,
    unfreeze : unfreeze
}



export default Utils;