import React, { useState, useRef } from "react";
import './style.css';

const ButtonComponent = ({
  labelText,
  disabled,
  onClick,
  ...rest
}) => {

  const getClassName = () =>{

    let className = "button-container";

    if (disabled === true){
      className = className + " disabled";
    }

    return className;
  }
    
  return (
    <div className="button-container">

      <button 
        type="button"
        disabled={disabled}
        onClick={onClick}
        className={disabled === true ? "disabled" : null}
      >
       {labelText}
      </button>
    </div>
  )
};

export default ButtonComponent;