import React from "react";

import "./style.css";


export const NGISQuestionAnswerSummaryComponent = ({
  data,
  ...rest
}) => {

    

    const displayData = () => {
        return (
 
            data.map((dataItem, index) => {
                return (
                    <div className="question-answer-line d-flex" key={index}>
                        <span className="questionnaire-answer dots-bg">
                            <span className="question-text white-bg"> 
                                {dataItem.question}
                            </span>
                        </span>
                        <p className="question-value white-bg">
                            {dataItem.answer}
                        </p>
                    </div>
                    
                )
                }
            )
          
          
         
        )
    }

  
  return (
  
    <div className='ngis-question-answer-summary-wrapper'>
        { displayData() }
    </div>
      
  )
};

export default NGISQuestionAnswerSummaryComponent;