import React, { useContext, createContext, useReducer } from "react"
import {stateProvider} from './stateProvider';
//import mainReducer from '../reducers/mainReducer'
import combineReducersUsingGlobalState from '../../reducers/combineReducers'
import ngisPanelReducer from '../../reducers/ngisPanelReducer'
import questionnaireReducer from '../../reducers/questionnaireReducer'
import ngisRadioButtonReducer from '../../reducers/ngisRadioButtonReducer'
import eventsReducer from "../../reducers/eventsReducer"
import clearValuesReducer from '../../reducers/clearValuesReducer'
import validatorReducer from '../../reducers/validatorReducer'
import updateComponentStateValueReducer from '../../reducers/updateComponentStateValueReducer'
import updateGlobalVariableReducer from '../../reducers/updateGlobalVariableReducer'
import ngisFileUploadReducer from '../../reducers/ngisFileUploadReducer'

const allReducers = combineReducersUsingGlobalState(
  {

    "questionnaireReducer" : questionnaireReducer,
    "updateGlobalVariableReducer": updateGlobalVariableReducer,
    "updateComponentStateValueReducer" : updateComponentStateValueReducer,
    "ngisRadioButtonReducer" : ngisRadioButtonReducer, 
    "ngisFileUploadReducer" : ngisFileUploadReducer,
    "ngisPanelReducer" : ngisPanelReducer,
    "clearValuesReducer" : clearValuesReducer,
    "validatorReducer" : validatorReducer,
    //this must be processed close to last
    "eventsReducer" : eventsReducer,
 
  }
  
);

const startiingState = {
  questionnaire : {}
};

export const ApplicationStore = ({ children }) => {
    const [state, dispatch] = useReducer(
      allReducers,
      startiingState
      
    );
  
    window.AppGlobalState = state; //for debugging
    stateProvider.current = state;
    window.AppStateProvider = stateProvider; //for debugging

    const getValue = (callback) => {
      return callback(state)
    };
  
    return (
      <AppStateContext.Provider value={{getValue, dispatch}}> 
          {children}
      </AppStateContext.Provider>
    );
  };

  export const AppStateContext = createContext();

  export const useSelector = (callback) => {
    const {getValue} = useContext(AppStateContext);
    return getValue(callback)
 }

 export const useDispatch = () => {
    const {dispatch} = useContext(AppStateContext);
    return dispatch
 }

 export const flags = {

    init : false
 }