import {questionnaireInputData} from '../services/questionnaireInputData'
import {processVisibleWhen} from './processVisibleWhen'
import {processEnableWhen} from './processEnableWhen'
import canRunIf from '../common/processRunIfCondition'

//evaulators will evaluate conditions like enableWhen, visibleWhen etc
const eventsEvaluator = (currentState, newState)=>{

    //look at all evaluators in system.
    //access and change them

    //look at each question
    //check for evaluator
    //change evaluator

    const questionnaire = questionnaireInputData.normalized;

    const elementIds = Object.keys(questionnaire);

    elementIds.forEach(id => {
        
        const element = questionnaire[id];

        if (typeof element.visibleWhen !== "undefined"){
            processVisibleWhen(currentState, newState, element);
        }
        if (typeof element.enableWhen !== "undefined"){
            
            processEnableWhen(currentState, newState, element);
        }

    });


}

export default eventsEvaluator;

export const actionConditionEvaluator = (action, currentState, newState) => {

    return canRunIf(action, currentState, newState);


}








