import React from "react";
import './style.css';


export const LabelComponent = ({
  labelText,
  bold,
  labelStyle,
  ...rest
}) => {
  const clickHandle = () =>{
    console.log("clicked on label");
  }

  const getLabelClassName = () =>{

    let className = "";

    if (bold === true){
      className = className + " bold";
    }

    if (labelStyle){
      className = className + " " + labelStyle;
    }

    return className;
  };

  const getLabelContainerClassName = () =>{

    let className = "label-container";

    if (labelStyle){
      className = className + " " + labelStyle;
    }

    return className;
  };

  return (
    <div className={getLabelContainerClassName()}>
      <label className={getLabelClassName()}>{labelText}</label>      
    </div>
  )
};