import React, { useState, useRef } from "react";
import SectionComponent from './SectionComponent';
import {useSelector} from '../../services/stateManagement/store'

export const SectionContainer = (props) => {

    const defaultVisible = true;

    let currentVisible = useSelector((state)=> {  
        if (state && state.questionnaire && state.questionnaire[props.id] && (typeof state.questionnaire[props.id].visible !== "undefined")){
            return state.questionnaire[props.id].visible;
        }
        return defaultVisible;
      
  });
    
    
        
    return (

        <SectionComponent id={props.id} children={props.children} visible={currentVisible}>
            
        </SectionComponent>


  )
};