 import { PageSectionContainer } from '../questionnaireElements/PageSection/PageSectionContainer';
import * as ngisPanelActions from './NgisPanelActions'
import {clearValuesAction, clearValidationResult, clearEnsusingValuesAction} from './ClearValuesActions' 
import {validateComponentData} from './ValidatorActions'
import {updateComponentStateValueAction, updateComponentStatePropertyAction} from './StateUpdateActions'
import {updateGlobalVariableAction} from './UpdateGlobalVariableAction'
import {setNGISRadioButtonSelectedData} from './NgisRadioButtonActions'


 export const ActionMapping = {
    "ngis-panel/move-to-panel-step" : ngisPanelActions.ngisPanelMoveToPanelStep,
    "ngis-panel/move-next" : ngisPanelActions.ngisPanelMoveNextAction, 
    "questionnaire/clear-values" : clearValuesAction,
    "validator/validate" : validateComponentData,
    "app/update-component-state-value": updateComponentStateValueAction,
    "app/update_global_variable" : updateGlobalVariableAction,
    "app/update-component-property" : updateComponentStatePropertyAction,
    "questionnaire/clear-validation-result": clearValidationResult,
    "questionnaire/clear-ensuing-values" : clearEnsusingValuesAction,
    "ngis-radiobutton/set-selected-data": setNGISRadioButtonSelectedData
 }

 export const actionMapper = (actionName) => {

    const action = ActionMapping[actionName];

    if (typeof action === "undefined"){
        throw ("Cannot map action " + actionName);
    }

    return action;

 };

 export default actionMapper;