import * as types from '../actions/ActionTypes'
import produce from 'immer'
import ConditionalEvaluator from '../common/ConditionalEvaluator'

export default function ngisRadioButtonReducer(state, action){

    const nextState = produce(state, draft => {
        
        var canUpdate = checkCondition(action.condition, state);

        switch (action.type){
            case types.NGIS_RADIOBUTTON_SELECTED_DATA:
            case types.NGIS_RADIOBUTTON_SELECTED: {
               
                if (canUpdate){
                    // console.log("In radio button reducer");
                    const radioButtonId = action.id;
                    const selectedValue = action.selectedValue;
                    const selectedIndex = action.selectedIndex;
                    //console.log("Parent PanelId" + parentPanelId);
                    draft.questionnaire[radioButtonId].value= {
                        selectedValue : selectedValue,
                        selectedIndex : selectedIndex
                    }
                }
                
                break;
            }
            

            default:
                break;
        }



    });
    //console.log("Next state in radio reducer is " + JSON.stringify(nextState));
    return nextState;
    

}

const checkCondition = (condition, state)=>{
    if (typeof condition === "undefined" || condition === null){
        return true;
    }

    if (Array.isArray(condition) && condition.length === 0){
        return true;
    }
    return ConditionalEvaluator.evaluate(condition, null, state);
}