import React from 'react'
import {ValidatorContainer} from './ValidatorContainer';


export function ValidatorProvider(item){

    return (

        <ValidatorContainer {...item} />

    )
}

export function ValidatorStateInitializer(item){
    
    const initialState = {
        validationGroup : item.validationGroup,
        validationData : []
        
    };

    item.validationData.forEach(data => {
        
        const validationItem = {
            type : data.type,
            targetItemId : data.targetItemId,
            conditions : data.conditions,
            validationMessage : data.validationMessage
        };
        initialState.validationData.push(validationItem);

    });

    return initialState;


}