import {questionnaireInputData} from '../services/questionnaireInputData'
import extractElementValue from '../common/extractElementValue'


//evaulators will evaluate conditions like enableWhen, visibleWhen etc
const validatorProcessor = (ids, currentState, newState)=>{


    const questionnaire = questionnaireInputData.normalized;

    //const elementIds = Object.keys(questionnaire);

    const result = [];
  

    ids.forEach(id => {
        
        const validatorItem = questionnaire[id];

        const validationGroup = validatorItem.validationGroup;

        let isValid = true;

        let messages = [];

        validatorItem.validationData.forEach(validationDataItem => {

            const result = validateItem(validationDataItem, currentState);

            isValid = isValid && result.isValid;

            if (!isValid){
                messages.push(result.message);
            }

            
        });

        
        result.push({
            validationGroup : validationGroup,
            isValid : isValid,
            messages : messages
        });
        

    });

   return result;
    


}

export default validatorProcessor;

const validateItem = (validationDataItem, currentState) =>{

    let isValid = true;
    let validationMessage = null;

    if (validationDataItem.type.toLowerCase() === "required"){

        isValid = requireValidator(validationDataItem.targetItemId, validationDataItem.property, currentState);

        console.log("Require validator result " + isValid)

        if (isValid === false){
            validationMessage = validationDataItem.validationMessage;
        }
    }

    return {
        isValid : isValid,
        message : validationMessage
    }

}


const requireValidator = (itemId, property, currentState) => {
    //debugger;
    const value = extractElementValue(itemId, property, currentState);

    if (value === null || value === undefined){
        return false;
    }

    if (typeof value === "string"){
        if (/^\s*$/.test(value)) {
            //string contains 0+ spaces only
            return false;
        }
    }

    return true;
}