
const uploadedFilesInternal = [];
const fileInfoInternal=[];

export const fileUploadManager = {
    uploadedFiles:uploadedFilesInternal,
    fileInfo:fileInfoInternal,
    submitFiles  : ()=>{

        //TODO: implement submit fo file store
    },
    addFile : (fileName, uniqueName, data, hash)=>{

        var index = uploadedFilesInternal.findIndex(u=>u.fileName === fileName);
        if (index >=0){
            return false;
        }
        uploadedFilesInternal.push({fileName:fileName, uniqueName: uniqueName, fileData: data, fileHash:hash});
        return true;
    },
    removeFile : (fileName) => {
        var index = uploadedFilesInternal.findIndex(u=>u.fileName === fileName);
        if (index > -1){
            uploadedFilesInternal.splice(index,1)
        }
    },
    addFileMetaData : (displayName, file)=>{
        var index = fileInfoInternal.findIndex(u=>u.displayName === displayName);
        
        console.log("Add file meta data called. " + displayName + " index =" + index);
        if (index === -1){
            fileInfoInternal.push({displayName:displayName, file : file});
            console.log("file meta added " + displayName);
        }

    },
    getFileMetaData : (displayName)=>{
        var index = fileInfoInternal.findIndex(u=>u.displayName === displayName);
        
        if (index>=0){
            return fileInfoInternal[index];
        }

        return null;

    },
    showFileMetaData: ()=>{

        return fileInfoInternal;
    }
    




}

window.fileUploadManager = fileUploadManager;