
// Data should be passed in a format like this
// var data=[
//     "AND",
//     { expression },
//     { expression},
//     [
//      "OR",
//       { expression},
//       { expression},	
//     ]
//    ];

import LogicalEvaluator from "./LogicalEvaluators";


var ConditionalEvaluator = {};

ConditionalEvaluator.isLogic = function(logic) {
    return (
      typeof logic === "object" && // An object
      logic !== null && // but not null
      ! Array.isArray(logic) && // and not an array
      Object.keys(logic).length === 1 // with exactly one key
    );
  };

  //operator is always first
  ConditionalEvaluator.getOperator = function(logic) {
    return Object.keys(logic)[0];
  };

  //evaluate must return true or false
  ConditionalEvaluator.evaluate = (logic, previousLogicalValue, state) => {

    
        //first check if it is an array
        if(!Array.isArray(logic)) {
            return evaluateLogic(logic, state);
        }

        if (logic.length === 1){
           return evaluateLogic(logic[0], state);
        }

        const conditionalOperator = logic[0]; 

        let cummulativeResult = null;

        for (let index = 1; index < logic.length; index++) {
            const logicItem = logic[index];

            if (!Array.isArray(logicItem)){

                const result = evaluateLogic(logicItem, state);

                if (cummulativeResult == null){
                    cummulativeResult = result;
                }else{
                    cummulativeResult = conditional(conditionalOperator, cummulativeResult, result);
                }
            }else{

                const result = ConditionalEvaluator.evaluate(logicItem, null, state);
                if (cummulativeResult == null){
                    cummulativeResult = result;
                }else{
                    cummulativeResult = conditional(conditionalOperator, cummulativeResult, result);
                }
            }
            
        }

        var conditionResult = cummulativeResult;
        
        return conditionResult;
       

    };


    const evaluateLogic = (logicItem, state)=>{

        if ("result" in logicItem){
            //for testing conditions
         
            return logicItem["result"];
        }
        return LogicalEvaluator(logicItem, state);

    };

    const conditional = (conditionalOperator, param1 , param2) =>{

        const isParam1Boolean = typeof param1 === 'boolean';
        const isParam2Boolean = typeof param2 === 'boolean';

        if (param1 === null && isParam2Boolean === true){            
            return param2;
        }else if (param2 === null && isParam1Boolean === true){
            return param1;
        }else if (isParam1Boolean === false && isParam2Boolean === false){
            throw new Error('Invalid boolean passed to conditional evaluator ' + param1 +  " : " + param2);
        }

        switch (conditionalOperator.toLowerCase()){

            case "and":
                return param1 && param2;
            case "or":
                //debugger;
                return param1 || param2;
            default:
                throw new Error("unsupported conditional operator");
        }

    };

    //TODO : remove. Useful for debugging
window.ConditionalEvaluator = ConditionalEvaluator;
export default ConditionalEvaluator;

