import React, { useState, useRef } from "react";
import renderComponentModel from '../../services/rendering/ComponentRenderer'

import "./style.css";

const PageComponent = (
{
    id,
    children,
    pageTitle,
    ...rest
}) => {

   
   

    const renderChildren = () => {
        console.log("Page : Rendering children");
        if (children){
            
            return (
                <React.Fragment>
                    {children.map((value, index) => {
                        return (
                            <div className="page-item" key={index}>
                                <h2>{pageTitle}</h2>
                                {renderComponentModel(value)}    
                            </div>
                        )
                    })}
                </React.Fragment>

            )
    
        }
        return  (<React.Fragment></React.Fragment>)
    }

    

    return (
        <div className="page">
            {renderChildren()}   
        </div>
        
    )


}

export default PageComponent;