import React, { useState, useRef, useContext } from "react";
import { NGISPanelStepComponent } from "./NGISPanelStepComponent";
import {
  useSelector,
  useDispatch,
  AppStateContext,
} from "../../services/stateManagement/store";
import * as ngisPanelActions from "../../actions/NgisPanelActions";
import { clearValuesAction } from "../../actions/ClearValuesActions";
import actionMapper from "../../actions/ActionMapping";

export const NGISPanelStepContainer = (props) => {
  const panelState = props.options.panelState;
  const panelIndex = props.options.panelIndex;
  const isLast = props.options.isLast;
  const parentPanelId = props.options.parentPanelId;
  const panelStepNumber = props.options.panelStepNumber;

  //const {dispatch} = useContext(AppStateContext);

  const dispatch = useDispatch();

  const panelStepClick = (eventData) => {
    console.log(JSON.stringify(eventData));

    if (!Array.isArray(eventData.actions)) {
      //default action
      console.warn("dispatching default");
      dispatch(
        ngisPanelActions.ngisPanelMoveToPanelStep({
          panelIndex: eventData.stepId,
          parentPanelId: parentPanelId,
        })
      );
    } else {
      const actionList = [];

      eventData.actions.forEach((actionItem) => {
        if (actionItem.action === "ngis-panel/move-to-panel-step") {
          actionList.push(
            ngisPanelActions.ngisPanelMoveToPanelStep({
              panelIndex: actionItem.data.stepId,
              parentPanelId: parentPanelId,
            })
          );
        } else if (actionItem.action === "questionnaire/clear-values") {
          actionList.push(clearValuesAction(actionItem.actionData));
        } else {
          let actionData = actionItem.actionData;
          let actionToCall = actionMapper(actionItem.action);
          let actionFunction = actionToCall(actionData);
          actionList.push(actionFunction);
        }
      });
      dispatch(actionList);
    }

    //dispatch(ngisPanelActions.ngisPanelMoveToPanelStep(data.stepId, parentPanelId));
    // dispatch(panelStepActions.ngisPanelMoveToPanelStep(index));
  };

  const summary = useSelector((state) => {
    if (state && state.questionnaire && state.questionnaire[props.id]) {
      return state.questionnaire[props.id].summary;
    }
    return "";
  });

  return (
    <NGISPanelStepComponent
      id={props.id}
      children={props.item}
      label={props.label}
      panelIndex={panelIndex}
      currentState={panelState}
      isLast={isLast}
      summaryButtonAction={props.summaryButtonAction}
      panelStepClick={panelStepClick}
      panelStepNumber={panelStepNumber}
      summary={summary}
      displayedPanelStepId={props.displayedPanelStepId}
    ></NGISPanelStepComponent>
  );
};
