
import produce from 'immer'
import eventsEvaluator from '../common/evaluators'

export default function eventsReducer(state={}, action){

    const nextState = produce(state, draft => {

        if (action.actionContext && Array.isArray(action.actionContext)){

            if (action.actionContext.indexOf("eventsReducer")> -1){
                eventsEvaluator(state, draft);
            }
        }
    });
    
    
    return nextState;
    

}

