import React, { useState, useRef } from "react";
import './style.css';
import SUCCESS_CIRCLED from '../../assets/img/success-circled.app.png';
import ICON_WARNING from '../../assets/img/icon-warning.app.svg';
import ICON_ERROR from '../../assets/img/icon-error.app.svg';
import ICON_INFO_BLUE from '../../assets/img/icon_info_blue.app.svg';


const NGISInfoMessageComponent = ({
    id,
    textMessage,
    messageType,
    ...rest
}) => {


    var listClassName = messageType==="Error" ? "message-error-line" : messageType==="Green" ? "message-green-line" : messageType==="Blue" ? "message-blue-line" : "message-line";
    var listStyleBackground = null;
    var style = null
    
    if (listClassName === "message-line") {
        listStyleBackground = ICON_WARNING;
        style = {
            background: "url(" + listStyleBackground + ") left center no-repeat",
        };
    }
    if (listClassName === "message-error-line") {
        listStyleBackground = ICON_ERROR;
        style = {
            background: "url(" + listStyleBackground + ") left center no-repeat",
        };
    }
    if (listClassName === "message-green-line") {
        listStyleBackground = SUCCESS_CIRCLED;
        style = {
            background: "url(" + listStyleBackground + ") left center no-repeat",
        };
    }
    if (listClassName === "message-blue-line") {
        listStyleBackground = ICON_INFO_BLUE;
        style = {
            background: "url(" + ICON_INFO_BLUE + ") left center no-repeat",
        };
    }
    

    console.log({messageType})


    return (
        <div className={messageType==="Error" ? "info-message-error-container" : messageType==="Green" ? "info-message-green-container" : messageType==="Blue" ? "info-message-blue-container" : "info-message-container"}>
            <ul className="message-list">
                <li className={listClassName} style={style}>{textMessage}</li>                
            </ul>
        </div>
    )
};

export default NGISInfoMessageComponent;