import React, { useState, useRef } from "react";

import {
  isValidDay,
  validateDay,
  validateMonth,
  validateYear,
  validateDate,
} from "./DateValidator";

import "./style.css";

export const DateInputComponent = ({
  currentValue,
  labelText,
  minDate,
  disabled,
  onChange: notify,
  customMessages,
  ...rest
}) => {
  const dayInput = useRef(null);
  const monthInput = useRef(null);
  const yearInput = useRef(null);

  const [fullDate, setFullDate] = useState({ month: currentValue.month, day: currentValue.day, year: currentValue.year });

  const [validationMessage, setValidationMessage] = useState("");

  const [validationResults, setValidationResults] = useState([]);

  const [validationErrorState, setValidationErrorState] = useState({
    day: false,
    month: false,
    year: false,
  });

  const inputs = {
    day: {
      next: monthInput,
      max: 2,
      ref: dayInput,
    },
    month: {
      prev: dayInput,
      next: yearInput,
      max: 2,
      ref: monthInput,
    },
    year: {
      prev: monthInput,
      max: 4,
      ref: yearInput,
    },
  };

  let dayStyle = "inputsCommon inputsDay";
  let monthStyle = "inputsCommon inputsMonth";
  let yearStyle = "inputsCommon inputsYear";

  const allowedBackwardActions = ["Backspace"];

  const moveForward = (inputType) => {
    const next = inputs[inputType].next;

    if (!next) return;
    next.current.focus();
    next.current.setSelectionRange(0, 0);
  };

  const moveBackward = (inputType) => {
    const prev = inputs[inputType].prev;

    if (!prev) return;
    prev.current.focus();
    prev.current.setSelectionRange(2, 2);
  };

  const isInput = ({ target, key, max }) => {
    if (isNaN(parseInt(key, 10))) return false;

    if (target.selectionStart === max) {
      return true;
    }
    return false;
  };

  const isDelete = ({ target, key }) => {
    if (!allowedBackwardActions.includes(key)) return false;

    if (target.selectionStart === 0) {
      return true;
    }
    return false;
  };

  const textIsSelected = (target) => {
    // stop any movement if there is text selected
    // we want to keep default functionality in this case
    const currentSelection = window.getSelection();

    return (
      currentSelection.toString().length > 0 ||
      target.selectionStart !== target.selectionEnd
    );
  };

  const handleKeyUp = ({ event, inputType, max }) => {
    const { target, key } = event;

    if (textIsSelected(target)) return;

    // we need to move forward on keyUp as well as keyDown
    // so that if a field is already at it's max we doni't lose the value
    // when we move to the next field

    if (target.selectionStart != target.value.length) {
      return;
    }

    if (isInput({ target, key, max })) moveForward(inputType);
  };

  const handleKeyDown = ({ event, inputType, max }) => {
    const { key, target } = event;

    if (textIsSelected(target)) return;

    if (key === "ArrowLeft" && target.selectionStart === 0) {
      moveBackward(inputType);
      event.preventDefault();
    }

    if (key === "ArrowRight" && target.selectionStart === target.value.length) {
      moveForward(inputType);
      event.preventDefault();
    }

    if (
      !(
        key === "ArrowLeft" ||
        key === "ArrowRight" ||
        key === "Tab" ||
        key === "Delete" ||
        key === "Backspace"
      ) &&
      target.value.length === max &&
      target.selectionStart < max
    ) {
      event.preventDefault();
      return;
    }

    if (
      !(
        key === "ArrowLeft" ||
        key === "ArrowRight" ||
        key === "Tab" ||
        key === "Delete" ||
        key === "Backspace"
      ) &&
      target.value.length === max
    ) {
      var next = inputs[inputType].next;
      var nextTargetValue = next ? inputs[inputType].next.current.value : "";
      if (nextTargetValue.length === 2 || nextTargetValue.length === 4) {
        moveForward(inputType);
        event.preventDefault();
        return;
      }
    }

    if (isInput({ target, key, max })) moveForward(inputType);
    if (isDelete({ target, key })) moveBackward(inputType);
  };

  const handleChange = ({ event, inputType }) => {
    event.preventDefault();
    const {
      target: { value },
    } = event;


    if (value !== "" && isNaN(parseInt(value, 10))) return false;

    const regexp = new RegExp(`^-?[0-9]*$`);
    if (value !== "" && !regexp.test(value)) {
      return;
    }

    if (value.length > inputs[inputType].max) {
      return;
    }

    const newDate = { ...fullDate, [inputType]: value };

    setFullDate(newDate);

    const returnValue = {
      ...newDate,
      date: new Date(Object.values(newDate)),
    };

    const results = validateDate(newDate, minDate, customMessages);
    setValidationResults(results);
    setErrorUI(results);

     validate(newDate, minDate);
  };

  const handlePaste = ({ event, inputType }) => {

    let value = (event.clipboardData || window.clipboardData).getData("text");

    const regexp = new RegExp(`^-?[0-9]*$`);

    value = value.replace(/\D/g, "");

    if (value !== "" && !regexp.test(value)) {
      return;
    }

    if (fullDate.day || fullDate.month || fullDate.year) {
      var newDate = {};

      if (value.length > inputs[inputType].max) {
        newDate = {
          ...fullDate,
          [inputType]: value.substring(0, inputs[inputType].max),
        };
      } else {
        newDate = { ...fullDate, [inputType]: value };
      }
      setFullDate(newDate);
      validate(newDate, minDate);
      return;
    }

    if (value.length > 7) {
      const newDate = {
        day: value.substring(0, 2),
        month: value.substring(2, 4),
        year: value.substring(4, 8),
      };

      setFullDate({ ...newDate });
      validate(newDate, minDate);
    } else {
      var newDate = {};
      if (value.length > inputs[inputType].max) {
        newDate = {
          ...fullDate,
          [inputType]: value.substring(0, inputs[inputType].max),
        };
      } else {
        newDate = { ...fullDate, [inputType]: value };
      }
      setFullDate(newDate);
      validate(newDate, minDate);
    }
  };

  const validate = (newDate, dob) => {
    const results = validateDate(newDate, dob, customMessages);
    setValidationResults(results);
    setErrorUI(results);

    //console.log("results + " + results.length);

    const canProceed = results.length === 0;
    if (notify && canProceed) {
      const formattedDate = newDate.year + "-" + pad(newDate.month) + "-" + pad(newDate.day);
      let inputDate = new Date(formattedDate);
      inputDate = inputDate.toISOString();
      const returnDate = inputDate.split(".")[0] + "Z";
      //console.log("[Consent] return date " + returnDate);
      notify({year:newDate.year, month:newDate.month, day:newDate.day, isValid:true});
      console.log("Notify date");
    } else if (notify) {
      notify({year:newDate.year, month:newDate.month, day:newDate.day, isValid:false});
    } else {
      console.log("Consent notify date handle not found");
    }
  };

  const pad = (n) => {
    const intN = parseInt(n);
    return (intN < 10 ? '0' : '') + intN;
  };

  const handleBlur = ({ event, inputType }) => {};

  const events = ({ inputType, max }) => ({
    onKeyUp: (event) => handleKeyUp({ event, inputType, max }),
    onKeyDown: (event) => handleKeyDown({ event, inputType, max }),
    onChange: (event) => handleChange({ event, inputType, max }),
    onPaste: (event) => handlePaste({ event, inputType, max }),
    onBlur: (event) => handleBlur({ event, inputType, max }),
  });

  const setErrorUI = (validationData) => {
    if (!validationData) {
      console.error("validation data is null");
      return;
    }

    let dayError = false;
    let monthError = false;
    let yearError = false;
    for (var i = 0; i < validationData.length; i++) {
      var split = validationData[i].key.split("-");

      switch (split[0]) {
        case "d":
          dayError = true;
          break;
        case "m":
          monthError = true;
          break;
        case "y":
          yearError = true;
          break;
        case "dm" :
            dayError = true;
            monthError = true;
          break;
        case "dmy":
          dayError = true;
          monthError = true;
          yearError = true;
          break;
        default:
          break;
      }
    }

    const newValidationState = {
      day: dayError,
      month: monthError,
      year: yearError,
    };

    setValidationErrorState(newValidationState);
  };

  const getLabelClassNames = (el) => {
    if (el === "d") {
      return validationErrorState.day
        ? "dateLabel dateLabelError"
        : "dateLabel";
    }

    if (el === "m") {
      return validationErrorState.month
        ? "dateLabel dateLabelError"
        : "dateLabel";
    }

    if (el === "y") {
      return validationErrorState.year
        ? "dateLabel dateLabelError"
        : "dateLabel";
    }

    if (el === "legend") {
      return validationErrorState.day ||
        validationErrorState.month ||
        validationErrorState.year
        ? "legend legend-error"
        : "legend";
    }

    return "";
  };

  const getInputClassNames = (el) => {
    if (el === "d") {
      return validationErrorState.day
        ? "inputsCommon inputsDay inputsCommonError"
        : "inputsCommon inputsDay";
    }

    if (el === "m") {
      return validationErrorState.month
        ? "inputsCommon inputsMonth inputsCommonError"
        : "inputsCommon inputsMonth";
    }

    if (el === "y") {
      return validationErrorState.year
        ? "inputsCommon inputsYear inputsCommonError"
        : "inputsCommon inputsYear";
    }

    return "inputsCommon";
  };


  return (
    <div className="data-input-wrapper">

   
    <fieldset>
      <legend className={getLabelClassNames("legend")}>
        {labelText}
        <span className="required-icon">✱</span>
      </legend>
      <div>
        <div className="inputs">
          <span className="inputsSpan">
            <label>
              <div className={getLabelClassNames("d")}>Day</div>
              <input
                max={2}
                name="consent-day"
                placeholder={"DD"}
                ref={dayInput}
                {...events({ inputType: "day", max: 2 })}
                value={fullDate.day}
                className={getInputClassNames("d")}
                style={{ width: "70px" }}
                disabled={disabled}
              />
            </label>
          </span>
          <span className="inputsSpan">
            <label>
              <div className={getLabelClassNames("m")}>Month</div>
              <input
                max={2}
                name="consent-month"
                placeholder={"MM"}
                ref={monthInput}
                {...events({ inputType: "month", max: 2 })}
                value={fullDate.month}
                className={getInputClassNames("m")}
                style={{ width: "70px" }}
                disabled={disabled}
              />
            </label>
          </span>
          <span className="inputsSpanYear">
            <label>
              <div className={getLabelClassNames("y")}>Year</div>
              <input
                max={4}
                name="consent-year"
                placeholder={"YYYY"}
                ref={yearInput}
                {...events({ inputType: "year", max: 4 })}
                value={fullDate.year}
                className={getInputClassNames("y")}
                style={{ width: "130px" }}
                disabled={disabled}
              />
            </label>
          </span>
        </div>
        <div className="errorMessages">
          <ul>
            {validationResults.map((item) => (
              <li key={item.key}>
                {
                  <svg
                    fill="inherit"
                    fillRule="evenodd"
                    width="24"
                    height="24"
                    viewBox="0 0 24 24"
                  >
                    <path d="M12.00025,22.00025 C11.74425,22.00025 11.48825,21.90225 11.29325,21.70725 L2.29325,12.70725 C1.90225,12.31625 1.90225,11.68425 2.29325,11.29325 L11.29325,2.29325 C11.68425,1.90225 12.31625,1.90225 12.70725,2.29325 L21.70725,11.29325 C22.09825,11.68325 22.09825,12.31625 21.70725,12.70725 L12.70725,21.70725 C12.51225,21.90325 12.25625,22.00025 12.00025,22.00025 Z M4.41425,12.00025 L12.00025,4.41525 L19.58625,12.00025 L12.00025,19.58625 L4.41425,12.00025 Z M11.00025,8.00025 L11.00025,14.00025 L13.00025,14.00025 L13.00025,8.00025 L11.00025,8.00025 Z M12.00025,17.25025 C12.6906059,17.25025 13.25025,16.6906059 13.25025,16.00025 C13.25025,15.3098941 12.6906059,14.75025 12.00025,14.75025 C11.3098941,14.75025 10.75025,15.3098941 10.75025,16.00025 C10.75025,16.6906059 11.3098941,17.25025 12.00025,17.25025 Z"></path>
                  </svg>
                }
                {item.message}
              </li>
            ))}
          </ul>
        </div>
      </div>
    </fieldset>
    </div>
  );
};


