import {questionnaireInputData} from '../services/questionnaireInputData';
import Utils from '../common/HelperFunctions';

const extractElementValue = (id, property, currentState) => {

    const elementType = questionnaireInputData.normalized[id].type;
 
    let value = null;

    if (property && property.indexOf("rootProperty") !==-1){
        //deal with root property

        let rootProperty = property.replace(/rootProperty:/gi,"");

        return getRootValue(id, rootProperty, currentState);
    }
 
   
    
    switch (elementType.toLowerCase()){
         case "ngis-radiobutton" : {
             //console.log("GetELementValue " + id);
             //console.log("State =" + JSON.stringify(currentState));
             const item = currentState.questionnaire[id];
 
             if (!Utils.propertyExistAndHasValue(item, "value")){
                 return value;
             }
             
             if (typeof item.value === "undefined"){
                 return value;
             }
 
             //console.log(JSON.stringify(currentState.questionnaire[id]));
 
             if (Utils.propertyExistAndHasValue(currentState.questionnaire[id],"value")
                 && 
                 ( Utils.propertyExistAndHasValue(currentState.questionnaire[id].value,property))
             ){
                 value = currentState.questionnaire[id].value[property];
             }

             if (typeof value === "undefined" || value === undefined){
                 value = null;
             }
             
         }
             break;
         case "ngis-textinput":
         case "textinput" :  {

            const item = currentState.questionnaire[id];
            
            if (!Utils.propertyExistAndHasValue(item, "value")){
                return value;
            }
            
            if (typeof item.value === "undefined"){
                return value;
            }

            
            value = currentState.questionnaire[id].value;

             if (typeof value === "undefined" || value === undefined){
                 value = null;
             }
         }
         break;
         default:
             break;
 
     }
 
     return value;
 }


 const getRootValue = (id, property, state) =>{

    let returnValue = null;

    const item = state.questionnaire[id];
            
    if (!Utils.propertyExistAndHasValue(item, property)){
        return returnValue;
    }
    
    returnValue =  item[property];

    if (returnValue === undefined){
        return null;
    }
    return returnValue;
 }

 const getValue = (id, property, state)=>{

    let defaultValue = null;

    const item = state.questionnaire[id];
            
    if (!Utils.propertyExistAndHasValue(item, "value")){
        return defaultValue;
    }

    if (typeof item.value === "undefined"){
        return defaultValue;
    }

    const returnValue = state.questionnaire[id].value;

    if (typeof returnValue === "undefined" || returnValue === undefined){
        return defaultValue;
    }

    return returnValue;


 }

 export default extractElementValue;