import React from 'react';
import { NGISPanelContainer } from './NGISPanelContainer'
import { stateProvider} from '../../services/stateManagement/stateProvider'



export function NGISPanelProvider(item) {
    
    // needed for initial rendering
    const [panelVisibilityList, current] = generatePanelVisibilityList(item);

    item.panelVisibilityList = panelVisibilityList;
    item.current = current;

    return (

        <NGISPanelContainer {...item} />

    )
}

