export function NGISPanelStateInitializer(item){
    
    const initialState = {};

    const panelStepCount = item.panelsteps.length;

    initialState.visiblePanelSteps = panelStepCount;
   

    //build visible panel steps
    const [panelVisibilityList, current] = generatePanelVisibilityList(item);
    
    initialState.panelVisibilityList = panelVisibilityList;

    initialState.current = current;

    // overide if set
    if (typeof item.current !== "undefined"){
        initialState.current = item.current;
    }

    return initialState;



}

const generatePanelVisibilityList = (item) => {
    let initialVisiblePanelSteps = item.initialVisiblePanelSteps;
    const panelVisibilityList = [];

    let firstVisible = null;

    item.panelsteps.map((panelStepItem) => {
        
        var id = panelStepItem.id;
        var isVisible = initialVisiblePanelSteps.indexOf(id)!==-1; //includes and ie??
        if (firstVisible === null){
            firstVisible = id;
        }

        var obj={};
        obj[id]=isVisible;
        panelVisibilityList.push({id:id, visible: isVisible});

    });

    let current = firstVisible;

    if (typeof item.current !== "undefined"){
        current = item.current;
    }

    return [panelVisibilityList, current];
}