import React, { useEffect, useState } from 'react';
import './style.css';


const NGISSelectDropDownComponent = ({
    dropDownItems,
    placeholder,
    selectedValue,
    onChange,
    isDisabled,
}) => {

    const placeholderConst = "__placeholderValue";
    if (typeof placeholder === "undefined"){
        placeholder = {value:placeholderConst, name:"Please Select.."}
    }

    console.log("NGIS Drop down = " + JSON.stringify(dropDownItems));
    console.log("Selected value = " + selectedValue);

    const defaultSelectValue = selectedValue ? selectedValue : placeholder.value;
    console.log("Default selected value" + defaultSelectValue);

    const stateContainer = {
        selectedValue : defaultSelectValue,
        showOptionList : false
    }

    const [state, setState] = useState(stateContainer);
    
    useEffect(() => {
        initializer();

        return function cleanUp(){
            removeInitializer();
        };
    });

    const handleClickOutside = e => {
        if (
          !e.target.classList.contains("custom-select-option") &&
          !e.target.classList.contains("selected-text")
        ) {
          setState(
            prevState => {
                return {
                  showOptionList: false,
                  selectedValue : prevState.selectedValue
                };
          });
        }
    };

    const handleListDisplay = () => {
        setState(prevState => {
          return {
            showOptionList: !prevState.showOptionList,
            selectedValue : prevState.selectedValue
          };
        });
    };

    const handleOptionClick = e => {
        console.log("Item selected " + e.target.getAttribute("data-value"));
        let newValue = e.target.getAttribute("data-value");
        setState({
          selectedValue: newValue,
          showOptionList: false
        });
        handleChange(newValue);
      };

    const initializer =()=>{
        
        document.addEventListener("mousedown", handleClickOutside);

        if (!(window.__consentGlobal && window.__consentGlobal.selectInit)){
            

            window.__consentGlobal = {
                selectInit : true
            }
        }       
    }

    const removeInitializer = ()=>{
        document.removeEventListener("mousedown", handleClickOutside);
    }

    

    const handleChange = (newValue) => {
        
        if (!onchange){
            onChange({selectedValue: newValue});

        }

    }

    

    const getSelectedText = ()=>{
        var selectedOption = state.selectedValue;
        console.log("State selection option " + selectedOption);
        if (selectedOption === null || selectedOption === undefined || selectedOption ===""){
            return placeholder.name;
        }

        if (selectedOption === placeholderConst){
            return placeholder.name;
        }

        var returnValue = ""; 
        
        dropDownItems.forEach(element => {
            if (element.value === selectedOption){
                returnValue = element.name;
            }
        }); 

        if (returnValue === ""){
            return placeholder.name;
        }

        return returnValue;

    }

   




    return (
        
        <div className="custom-select-container">
            <div className={state.showOptionList ? "selected-text active" : "selected-text"} onClick={handleListDisplay}>
                {getSelectedText()}
            </div>
            {state.showOptionList && (
                <ul className="select-options">
                    {dropDownItems.map((option,index) => {
                    if (option.value !== state.selectedValue){
                        return (
                            <li
                                className="custom-select-option"
                                data-value={option.value}
                                key={option.value}
                                onClick={handleOptionClick}
                            >
                            {option.name}
                            </li>
                        );
                    }
                    
                    })}
            </ul>
        )}
        </div>
        
        
        
      
        
       
    )
};

export default NGISSelectDropDownComponent
