import * as types from '../actions/ActionTypes'
import produce from 'immer'
import { questionnaireInputData } from '../services/questionnaireInputData'
import { processTemplate, processQuestionAnswerTemplate } from '../common/templateProcessor'
import ConditionalEvaluator from '../common/ConditionalEvaluator'

export default function ngisPanelReducer(state, action) {
    //console.log("In panel reducer");
    const nextState = produce(state, draft => {

        switch (action.type) {

            case types.NGIS_PANEL_MOVE_TO_PANEL_STEP: {
                var canUpdate = checkCondition(action.condition, state);
                if (canUpdate === true) {
                    const newIndex = action.panelIndex;
                    const parentPanelId = action.parentPanelId;
                    //console.log("Parent PanelId" + parentPanelId);
                    draft.questionnaire[parentPanelId].current = newIndex;

                    updateSummaryForPanelSteps(state, draft, parentPanelId);

                    console.log(`Move to panel step called parent panel Id=${parentPanelId} newIndex=${newIndex}`);
                }

                break;
            }
            case types.NGIS_PANEL_MOVE_NEXT: {
                const parentPanelId = action.parentPanelId;

                let currentId = state.questionnaire[parentPanelId].current;
                const visiblePanelCount = state.questionnaire[parentPanelId].visiblePanelSteps;

                const panelVisibilityList = state.questionnaire[parentPanelId].panelVisibilityList;

                //console.log("Parent PanelId = " + parentPanelId + "  visiblePanelCount = " + visiblePanelCount + " currentIndex = "+ currentIndex + " state = " + JSON.stringify(state)); 
                if (typeof currentId === "undefined") {
                    currentIndex = panelVisibilityList.find(f => f.visible === true);
                    if (!currentId) {
                        return;
                    }
                }

                const visibleIds = panelVisibilityList.filter(f => f.visible == true).map(x => x.id);
                const currentIndex = visibleIds.indexOf(currentId);

                if (currentIndex !== -1 && currentIndex < visibleIds.length - 1) {
                    currentId = visibleIds[currentIndex + 1]
                }

                draft.questionnaire[parentPanelId].current = currentId;
                updateSummaryForPanelSteps(state, draft, parentPanelId);
                //console.log("draft.questionnaire[parentPanelId]="+draft.questionnaire[parentPanelId].current)
                console.log(`Panel Move Next Called  parent panel Id=${parentPanelId}currentId=${currentId}`);
            }

            default:
                break;
        }



    });

    return nextState;


}


const updateSummaryForPanelSteps = (state, draft, panelId) => {

    const panelState = state.questionnaire[panelId];

    const panelVisibilityList = state.questionnaire[panelId].panelVisibilityList;

    panelVisibilityList.forEach(element => {
        //console.log("considering summary " + element.id);
        var summaryProvider = questionnaireInputData.normalized[element.id].summaryValueProvider;

        //console.log("Summary provider " + summaryProvider);

        if (typeof summaryProvider !== "undefined") {
            console.log(summaryProvider.type.toLowerCase());
            switch (summaryProvider.type.toLowerCase()) {

                case "getelementproperty": {

                    var id = summaryProvider.elementId;
                    var property = summaryProvider.property;

                    //console.log("In get element property " + id);

                    try {
                        var questionnaireStateNode = state.questionnaire[id];

                        if (typeof questionnaireInputData === "undefined") {
                            return;
                        }

                        var value = null;

                        if (typeof questionnaireStateNode.value !== undefined) {
                            value = questionnaireStateNode.value[property];
                        }

                        console.log(JSON.stringify(questionnaireStateNode));

                        console.log("Property value " + value);

                        if (typeof value !== "undefined") {
                            draft.questionnaire[element.id].summary = value;
                        }


                    } catch (e) {
                        console.error("Unable to set summary. Getting data from element " + element + " and property =" + property);
                    }

                }
                    break;
                case "template":
                case "basictemplate": {

                    var results = processTemplate(summaryProvider, state);

                    if (results.length > 1) {
                        draft.questionnaire[element.id].summary = results.join("<br>");
                    } else {
                        draft.questionnaire[element.id].summary = results[0];
                    }

                }
                    break;
                case "questionanswertemplate": {
                    var results = processQuestionAnswerTemplate(summaryProvider.questionAnswerTemplateData, state);
                    draft.questionnaire[element.id].summary = { type: "questionanswertemplate", "data": results };

                }
                    break;
                default:
                    break;
            }
        }


    });
}

const checkCondition = (condition, state)=>{
    if (typeof condition === "undefined" || condition === null){
        return true;
    }

    if (Array.isArray(condition) && condition.length === 0){
        return true;
    }
    return ConditionalEvaluator.evaluate(condition, null, state);
}