import React, { useState, useRef } from "react";
import renderComponentModel from '../../services/rendering/ComponentRenderer'

import "./style.css";

export const LayoutComponent = (
{
    layout,
    children,
    ...rest
}) => {

   const parseLayout = (value) => {

        var tempLayout = true;
        if (value !== null && value !== undefined){
            const temp = value.toLowerCase();
            if (temp === "vertical"){
                tempLayout = false;
            }else if (temp === "horizontal"){
                tempLayout = true;
            }else{
                throw "Invalid layout mode " + value;
            }
        }

        return tempLayout;
   };

   const isHorizontal = parseLayout(layout);

   //const [isHorizontalLayout, setIsHorizontalLayout] = useState(isHorizontal);

    const getContainerClass = () => {

        if (isHorizontal){
            return "layout-component-horizontal"
        }
        return "layout-component-vertical";

    };

    const renderChildren = () => {
        console.log("Rendering children");
        if (children){
            
            return (
                <React.Fragment>
                    {children.map((value, index) => {
                        return (
                            <div className="layout-item" key={index}>
                                {renderComponentModel(value)}    
                            </div>
                        )
                    })}
                </React.Fragment>

            )
    
        }
        return  (<React.Fragment></React.Fragment>)
    }

    

    return (
        <div className={getContainerClass()}>
            {renderChildren()}   
        </div>
        
    )


}