import React, { useState, useRef } from "react";
import './style.css'


export const TextAreaComponent = ({
  labelText,
  orientation,
  disabled,
  placeholder,
  required,
  rows,
  cols,
  defaultvalue,
  maxlength,
  onChange: notify,
  ...rest
}) => {
  const textAreaRef = useRef(null);

  const [currentText, setCurrentText] = useState("");

  if(!Number.isInteger(maxlength)){
    maxlength = 150;
  }

  if (!Number.isInteger(rows)) {
    rows = 4;
  }

  if (!Number.isInteger(cols)) {
    cols = 30;
  }

  if (placeholder === null || placeholder == undefined) {
    placeholder = "";
  }

  let isInline = false;

  const parseOrientation = (orientationText) => {
    try {
      const temp = orientationText.toLowerCase();
      if (temp === "inline") {

        return true;
      } else if (temp === "top") {
        return false;
      }
    } catch (e) {
      console.log(e);
      throw 'Unable to parse orientation';

    }
  }

  if (orientation !== null && orientation !== undefined) {
    isInline = parseOrientation(orientation);
  }

  const handlePaste = ({ event, inputType }) => {

  };


  const events = ({ inputType, max }) => ({
    onPaste: (event) => handlePaste({ event, inputType, max }),
  });


  const getTextInputContainerClass = () => {

    if (isInline) {
      return 'textarea-container-horizontal';
    } else {
      return 'textarea-container-vertical';
    }

  }; 

  return (
    <div className={getTextInputContainerClass()}>
      <label>{labelText}</label>
      <textarea
        rows={rows}
        cols={cols}
        ref={textAreaRef}
        disabled={disabled}
        placeholder={placeholder}
        maxlength={maxlength}
      >
          {defaultvalue}
      </textarea>          
    </div>
  )
};