import React, { useState, useRef } from "react";
//import SignaturePad from 'react-signature-pad';
import SignaturePad from '../../components/SignaturePad';
import "./style.css";



export const SignatureComponent = ({
  labelText,
  updateSignaureCallback,
  signatureData,
  ...rest
}) => {

    const signatureRef = useRef(null);

    const clearPad = () =>{
       var signature = signatureRef.current;

       if (signature !== undefined){
           signature.clear();

           updateSignaureCallback({event:"clear"});
       }

    };

    if (signatureRef.current != null && typeof signatureData !== "undefined" ){
      signatureRef.current.fromDataURL(signatureData);
    }

    const signatureUpdate = () => {
        var signature = signatureRef.current;

        if( signature !== undefined ){
            updateSignaureCallback({event:"save", data : signature.toDataURL()});
        }
    };

    const showSignaturePad = () => {
        return (
          <div>
            
            <div className="signature-app" >
              <SignaturePad ref={signatureRef} onEnd={signatureUpdate} penColor="#005EB8"/>
              <button onClick={ () => clearPad() } className="btn signature-clear-button"> Clear </button>
              <div style={{clear:'both'}}></div>
              <div style={{'borderTop': '2px solid #425563'}}></div>
              <span className="signature-label">
                <h5>{ labelText }</h5>
              </span>
            </div>
            
            </div>
        )
    }

  
  return (
  
    <div className='signature-pad-app-wrapper'>
        { showSignaturePad() }
    </div>
      
  )
};

export default SignatureComponent;